import client from './configAxios';
import { logger } from '../utils';
import { resetPassword } from './endpoint';
import { ChangePasswordPayload } from '../redux/features/changePassword/types';

export const fetchResetPassword = async (payload: ChangePasswordPayload) => {
  try {
    const response = await client.post(resetPassword, {
      ...payload,
      userType: 0,
    });

    return {
      status: response.status,
      code: response.status,
      data: response.data?.data,
    };
  } catch (error: any) {
    logger('error fetchResetPassword', error);
    return {
      message: error?.response?.data?.message,
      code: error?.code,
      status: error?.status,
    };
  }
};
