import { call, put, takeEvery } from 'redux-saga/effects';
import { logger } from '../../../utils';
import { HIDE_LOADING_OVERLAY, SHOW_LOADING_OVERLAY } from '../app/constants';
import {
  FORGOT_PASSWORD_ASYNC,
  FORGOT_PASSWORD_FAILED,
  FORGOT_PASSWORD_SUCCESS,
} from './contants';
import { fetchForgotPassword } from '../../../api/forgotPassword';
import { ResponseApi } from '../../store/types';
import { NavigationService } from '../../../services/NavigationService';
import i18n from '../../../i18n';
import { SCREENS_ENUM } from '../../../types/global';

function* handleForgotPassword(action: {
  type: string;
  payload: any;
}): Generator<any, void, any> {
  try {
    yield put({ type: FORGOT_PASSWORD_FAILED, payload: null });
    yield put({ type: SHOW_LOADING_OVERLAY });
    const response = (yield call(
      fetchForgotPassword,
      action.payload,
    )) as ResponseApi;
    if (response.status === 200) {
      yield put({ type: FORGOT_PASSWORD_SUCCESS });
      yield call(NavigationService.navigate, SCREENS_ENUM.VerifyOtpScreen, {
        email: action.payload,
      });
    } else {
      yield put({ type: FORGOT_PASSWORD_FAILED, payload: response.message });
    }
  } catch (error) {
    logger('error handleForgotPassword', error);
    yield put({
      type: FORGOT_PASSWORD_FAILED,
      payload: i18n.t('FORGOT_PASSWORD_FAILED'),
    });
  } finally {
    yield put({ type: HIDE_LOADING_OVERLAY });
  }
}

export default function* forgotPasswordSaga() {
  yield takeEvery(FORGOT_PASSWORD_ASYNC, handleForgotPassword);
}
