import {
  FETCH_HOUSE_HAS_JOB_WORKER_ASYNC,
  FETCH_HOUSE_HAS_JOB_WORKER_FAILED,
  FETCH_HOUSE_HAS_JOB_WORKER_PROGRESS,
  FETCH_HOUSE_HAS_JOB_WORKER_SUCCESS,
  TOGGLE_JOB_BOARD_CHECKBOX,
  TOGGLE_JOB_BOARD_FAV_ASYNC,
  TOGGLE_JOB_BOARD_FAV_FAILED,
  TOGGLE_JOB_BOARD_FAV_PROGRESS,
  TOGGLE_JOB_BOARD_FAV_SUCCESS,
  CLEAR_JOB_BOARD_STATE,
  REGISTER_JOBS_PROGRESS,
  REGISTER_JOBS_FAILED,
  REGISTER_JOBS_SUCCESS,
  REGISTER_JOBS_ASYNC,
  GET_COMMUNITY_USER_HAS_JOB_PROGRESS,
  GET_COMMUNITY_USER_HAS_JOB_FAILED,
  GET_COMMUNITY_USER_HAS_JOB_SUCCESS,
  GET_COMMUNITY_USER_HAS_JOB_ASYNC,
} from './contants';
import { FetchHouseHasJobWorkerParams } from '../../../api/jobBoard';

export const fetchHouseHasJobWorkerAsync = (
  payload: FetchHouseHasJobWorkerParams,
) => ({ type: FETCH_HOUSE_HAS_JOB_WORKER_ASYNC, payload } as const);

export const fetchHouseHasJobWorkerSuccess = (payload: any) =>
  ({ type: FETCH_HOUSE_HAS_JOB_WORKER_SUCCESS, payload } as const);

export const fetchHouseHasJobWorkerFailed = (payload: any) =>
  ({ type: FETCH_HOUSE_HAS_JOB_WORKER_FAILED, payload } as const);

export const fetchHouseHasJobWorkerProgress = (payload: boolean) =>
  ({ type: FETCH_HOUSE_HAS_JOB_WORKER_PROGRESS, payload } as const);

export const toggleJobBoardCheckbox = (payload: string) =>
  ({ type: TOGGLE_JOB_BOARD_CHECKBOX, payload } as const);

export const toggleJobBoardFavAsync = (payload: string) =>
  ({ type: TOGGLE_JOB_BOARD_FAV_ASYNC, payload } as const);

export const toggleJobBoardFavSuccess = (payload: any) =>
  ({ type: TOGGLE_JOB_BOARD_FAV_SUCCESS, payload } as const);

export const toggleJobBoardFavFailed = (payload: any) =>
  ({ type: TOGGLE_JOB_BOARD_FAV_FAILED, payload } as const);

export const toggleJobBoardFavProgress = (payload: boolean) =>
  ({ type: TOGGLE_JOB_BOARD_FAV_PROGRESS, payload } as const);

export const registerJobsAsync = (payload: {
  jobIds: string[];
  message: string;
}) => ({ type: REGISTER_JOBS_ASYNC, payload } as const);

export const registerJobsSuccess = (payload: any) =>
  ({ type: REGISTER_JOBS_SUCCESS, payload } as const);

export const registerJobsFailed = (payload: any) =>
  ({ type: REGISTER_JOBS_FAILED, payload } as const);

export const registerJobsProgress = (payload: boolean) =>
  ({ type: REGISTER_JOBS_PROGRESS, payload } as const);

export const getCommunityUserHasJobAsync = () =>
  ({ type: GET_COMMUNITY_USER_HAS_JOB_ASYNC } as const);

export const getCommunityUserHasJobSuccess = (payload: any) =>
  ({ type: GET_COMMUNITY_USER_HAS_JOB_SUCCESS, payload } as const);

export const getCommunityUserHasJobFailed = (payload: any) =>
  ({ type: GET_COMMUNITY_USER_HAS_JOB_FAILED, payload } as const);

export const getCommunityUserHasJobProgress = (payload: boolean) =>
  ({ type: GET_COMMUNITY_USER_HAS_JOB_PROGRESS, payload } as const);

export const clearJobBoardState = () =>
  ({ type: CLEAR_JOB_BOARD_STATE } as const);

export const actionJobBoard = {
  fetchHouseHasJobWorkerAsync,
  fetchHouseHasJobWorkerSuccess,
  fetchHouseHasJobWorkerFailed,
  fetchHouseHasJobWorkerProgress,
  toggleJobBoardCheckbox,
  toggleJobBoardFavAsync,
  toggleJobBoardFavSuccess,
  toggleJobBoardFavFailed,
  toggleJobBoardFavProgress,
  registerJobsAsync,
  registerJobsSuccess,
  registerJobsFailed,
  registerJobsProgress,
  clearJobBoardState,
  getCommunityUserHasJobAsync,
  getCommunityUserHasJobSuccess,
  getCommunityUserHasJobFailed,
  getCommunityUserHasJobProgress,
};

export type JobBoardAction = ReturnType<
  | typeof fetchHouseHasJobWorkerAsync
  | typeof fetchHouseHasJobWorkerSuccess
  | typeof fetchHouseHasJobWorkerFailed
  | typeof fetchHouseHasJobWorkerProgress
  | typeof toggleJobBoardCheckbox
  | typeof toggleJobBoardFavAsync
  | typeof toggleJobBoardFavSuccess
  | typeof toggleJobBoardFavFailed
  | typeof toggleJobBoardFavProgress
  | typeof clearJobBoardState
  | typeof registerJobsAsync
  | typeof registerJobsSuccess
  | typeof registerJobsFailed
  | typeof registerJobsProgress
  | typeof getCommunityUserHasJobAsync
  | typeof getCommunityUserHasJobSuccess
  | typeof getCommunityUserHasJobFailed
  | typeof getCommunityUserHasJobProgress
>;
