import { store } from '../redux';
import {
  GetTemplateContractDetailPayload,
  SaveSignatureContractPayload,
} from '../redux/features/contract/types';
import { logger } from '../utils';
import client from './configAxios';
import {
  getPendingContract,
  getSignedContract,
  getTemplateContractDetail,
  saveSignatureContract,
} from './endpoint';

export const fetchGetTemplateContractDetail = async (
  payload: GetTemplateContractDetailPayload,
) => {
  try {
    const language = store.getState().app.language;
    const { companyId, userType } = payload;

    const response = await client.get(
      `${getTemplateContractDetail}?companyId=${companyId}&userType=${userType}&language=${
        payload.language || language
      }`,
    );
    return {
      status: response.status,
      code: response.status,
      data: response.data?.data,
    };
  } catch (error: any) {
    logger('error fetchGetTemplateContractDetail', error);
    return {
      message: error?.response?.data?.message,
      code: error?.code,
      status: error?.status,
    };
  }
};

