export const fetchSaveSignatureContract = async (
  payload: SaveSignatureContractPayload,
) => {
  try {
    const accountId = store.getState().auth.userProfile?.accountId;
    const language = store.getState().app.language;

    const formData = new FormData();
    formData.append('AccountId', accountId);
    formData.append('CompanyId', payload.companyId);
    formData.append('UserType', payload.userType.toString());
    formData.append('Language', language);
    formData.append('SignatureFile', payload.signatureFile);

    const response = await client.put(saveSignatureContract, formData, {
      headers: {
        'Content-Type': 'multipart/form-data',
      },
    });
    return {
      status: response.status,
      code: response.status,
      data: response.data?.data,
    };
  } catch (error: any) {
    logger('error fetchSaveSignatureContract', error);
    return {
      message: error?.response?.data?.message,
      code: error?.code,
      status: error?.status,
    };
  }
};

