import {
  GET_COMPANY_ASYNC,
  GET_COMPANY_FAILED,
  GET_COMPANY_PROGRESS,
  GET_COMPANY_SUCCESS,
  REGISTER_COMPANY_ASYNC,
  REGISTER_COMPANY_SUCCESS,
  REGISTER_COMPANY_FAILED,
  REGISTER_COMPANY_PROGRESS,
  GET_COMPANY_TEST_ASYNC,
  GET_COMPANY_TEST_SUCCESS,
  GET_COMPANY_TEST_FAILED,
  GET_COMPANY_TEST_PROGRESS,
} from './contants';

// action saga
export const getCompanyAsync = () => ({ type: GET_COMPANY_ASYNC } as const);
export const registerCompanyAsync = (payload: any) =>
  ({ type: REGISTER_COMPANY_ASYNC, payload } as const);
export const getCompanyTestAsync = (payload: any) =>
  ({ type: GET_COMPANY_TEST_ASYNC, payload } as const);

// action reducer
export const getCompanySuccess = (payload: any) =>
  ({ type: GET_COMPANY_SUCCESS, payload } as const);
export const getCompanyFailed = (payload: any) =>
  ({ type: GET_COMPANY_FAILED, payload } as const);
export const getCompanyProgress = (payload: boolean) =>
  ({ type: GET_COMPANY_PROGRESS, payload } as const);

export const registerCompanySuccess = (payload: any) =>
  ({ type: REGISTER_COMPANY_SUCCESS, payload } as const);
export const registerCompanyFailed = (payload: any) =>
  ({ type: REGISTER_COMPANY_FAILED, payload } as const);
export const registerCompanyProgress = (payload: boolean) =>
  ({ type: REGISTER_COMPANY_PROGRESS, payload } as const);

export const getCompanyTestSuccess = (payload: any) =>
  ({ type: GET_COMPANY_TEST_SUCCESS, payload } as const);
export const getCompanyTestFailed = (payload: any) =>
  ({ type: GET_COMPANY_TEST_FAILED, payload } as const);
export const getCompanyTestProgress = (payload: boolean) =>
  ({ type: GET_COMPANY_TEST_PROGRESS, payload } as const);

export const actionRegisterCompany = {
  getCompanyAsync,
  getCompanySuccess,
  getCompanyFailed,
  getCompanyProgress,
  registerCompanyAsync,
  registerCompanySuccess,
  registerCompanyFailed,
  registerCompanyProgress,
  getCompanyTestAsync,
  getCompanyTestSuccess,
  getCompanyTestFailed,
  getCompanyTestProgress,
};

export type ReferFriendAction = ReturnType<
  | typeof getCompanyAsync
  | typeof getCompanySuccess
  | typeof getCompanyFailed
  | typeof getCompanyProgress
  | typeof registerCompanyAsync
  | typeof registerCompanySuccess
  | typeof registerCompanyFailed
  | typeof registerCompanyProgress
  | typeof getCompanyTestAsync
  | typeof getCompanyTestSuccess
  | typeof getCompanyTestFailed
  | typeof getCompanyTestProgress
>;
