import { takeEvery } from 'redux-saga/effects';
import {
  RESEND_CODE_OTP_ASYNC,
  RESEND_CODE_OTP_FAILED,
  RESEND_CODE_OTP_PROGRESS,
  RESEND_CODE_OTP_SUCCESS,
  VERIFY_EMAIL_OTP_ASYNC,
  VERIFY_EMAIL_OTP_FAILED,
  VERIFY_EMAIL_OTP_SUCCESS,
} from './contants';
import { call, put } from 'redux-saga/effects';
import { fetchVerifyEmailOtp } from '../../../api/verifyOtp';
import { ResponseApi } from '../../store/types';
import { HIDE_LOADING_OVERLAY, SHOW_LOADING_OVERLAY } from '../app/constants';
import { logger } from '../../../utils';
import { VerifyEmailOtpPayload } from './types';
import Toast from 'react-native-toast-message';
import i18n from '../../../i18n';
import { fetchForgotPassword } from '../../../api/forgotPassword';
import { NavigationService } from '../../../services/NavigationService';
import { SCREENS_ENUM } from '../../../types/global';

export function* handleVerifyOtp(action: {
  type: string;
  payload: VerifyEmailOtpPayload;
}): Generator<any, void, any> {
  try {
    yield put({ type: SHOW_LOADING_OVERLAY });

    const response = (yield call(
      fetchVerifyEmailOtp,
      action.payload,
    )) as ResponseApi;
    if (response.status === 200 && response.data) {
      yield put({ type: VERIFY_EMAIL_OTP_SUCCESS });
      yield call(
        NavigationService.replace,
        SCREENS_ENUM.ChangePasswordScreen,
        action.payload,
      );
    } else {
      yield put({
        type: VERIFY_EMAIL_OTP_FAILED,
        payload: i18n.t('VERIFY_CODE_FAILED'),
      });
      yield call(Toast.show, {
        text1: i18n.t('VERIFY_CODE_FAILED'),
        type: 'error',
      });
    }
  } catch (error) {
    logger('error handleVerifyOtp', error);
    yield put({
      type: VERIFY_EMAIL_OTP_FAILED,
      payload: i18n.t('VERIFY_CODE_FAILED'),
    });
    yield call(Toast.show, {
      text1: i18n.t('VERIFY_CODE_FAILED'),
      type: 'error',
    });
  } finally {
    yield put({ type: HIDE_LOADING_OVERLAY });
  }
}

export function* handleResendCodeOtp(action: {
  type: string;
  payload: string;
}): Generator<any, void, any> {
  try {
    yield put({ type: RESEND_CODE_OTP_PROGRESS, payload: true });
    const response = (yield call(
      fetchForgotPassword,
      action.payload,
    )) as ResponseApi;
    if (response.status === 200) {
      yield put({ type: RESEND_CODE_OTP_SUCCESS });
      yield call(Toast.show, {
        text1: i18n.t('RESEND_CODE_SUCCESS'),
        type: 'success',
      });
    } else {
      yield put({
        type: RESEND_CODE_OTP_FAILED,
        payload: i18n.t('OTP_SEND_FAILED'),
      });
      yield call(Toast.show, {
        text1: i18n.t('RESEND_CODE_FAILED'),
        type: 'error',
      });
    }
  } catch (error) {
    logger('error handleResendCodeOtp', error);
    yield put({
      type: RESEND_CODE_OTP_FAILED,
      payload: i18n.t('OTP_SEND_FAILED'),
    });
    yield call(Toast.show, {
      text1: i18n.t('RESEND_CODE_FAILED'),
      type: 'error',
    });
  } finally {
    yield put({ type: RESEND_CODE_OTP_PROGRESS, payload: false });
  }
}

export default function* verifyOtpSaga() {
  yield takeEvery(VERIFY_EMAIL_OTP_ASYNC, handleVerifyOtp);
  yield takeEvery(RESEND_CODE_OTP_ASYNC, handleResendCodeOtp);
}
