import { VerifyOtpAction } from './actions';
import {
  RESEND_CODE_OTP_FAILED,
  RESEND_CODE_OTP_PROGRESS,
  RESEND_CODE_OTP_RESET,
  RESEND_CODE_OTP_SUCCESS,
  VERIFY_EMAIL_OTP_FAILED,
  VERIFY_EMAIL_OTP_RESET,
  VERIFY_EMAIL_OTP_SUCCESS,
} from './contants';
import { VerifyOtpState } from './types';

export const initialState: VerifyOtpState = {
  email: {
    loading: false,
    error: null,
    success: false,
  },
  resendCode: {
    loading: false,
    error: null,
    success: false,
  },
};

export default function verifyOtpReducer(
  state = initialState,
  action: VerifyOtpAction,
): VerifyOtpState {
  switch (action.type) {
    case VERIFY_EMAIL_OTP_SUCCESS:
      return {
        ...state,
        email: { ...state.email, loading: false, success: true, error: null },
      };
    case VERIFY_EMAIL_OTP_FAILED:
      return {
        ...state,
        email: {
          ...state.email,
          loading: false,
          error: action.payload,
          success: false,
        },
      };
    case VERIFY_EMAIL_OTP_RESET:
      return {
        ...state,
        email: { ...initialState.email },
      };
    case RESEND_CODE_OTP_PROGRESS:
      return {
        ...state,
        resendCode: {
          ...state.resendCode,
          loading: action.payload,
        },
      };
    case RESEND_CODE_OTP_SUCCESS:
      return {
        ...state,
        resendCode: {
          ...state.resendCode,
          loading: false,
          success: true,
          error: null,
        },
      };
    case RESEND_CODE_OTP_FAILED:
      return {
        ...state,
        resendCode: {
          ...state.resendCode,
          loading: false,
          error: action.payload,
          success: false,
        },
      };
    case RESEND_CODE_OTP_RESET:
      return {
        ...state,
        resendCode: { ...initialState.resendCode },
      };
    default:
      return state;
  }
}
