import { JobBoardAction } from './actions';
import {
  CLEAR_JOB_BOARD_STATE,
  FETCH_HOUSE_HAS_JOB_WORKER_FAILED,
  FETCH_HOUSE_HAS_JOB_WORKER_PROGRESS,
  FETCH_HOUSE_HAS_JOB_WORKER_SUCCESS,
  GET_COMMUNITY_USER_HAS_JOB_FAILED,
  GET_COMMUNITY_USER_HAS_JOB_PROGRESS,
  GET_COMMUNITY_USER_HAS_JOB_SUCCESS,
  REGISTER_JOBS_FAILED,
  REGISTER_JOBS_PROGRESS,
  REGISTER_JOBS_SUCCESS,
  TOGGLE_JOB_BOARD_CHECKBOX,
  TOGGLE_JOB_BOARD_FAV_FAILED,
  TOGGLE_JOB_BOARD_FAV_PROGRESS,
  TOGGLE_JOB_BOARD_FAV_SUCCESS,
} from './contants';
import { JobBoardState } from './types';

const initialState: JobBoardState = {
  houseHasJobWorker: {
    isLoading: false,
    error: null,
    data: null,
  },
  communityUserHasJob: {
    isLoading: false,
    error: null,
    data: null,
  },
};

export default function jobBoardReducer(
  state = initialState,
  action: JobBoardAction,
): JobBoardState {
  switch (action.type) {
    case FETCH_HOUSE_HAS_JOB_WORKER_PROGRESS:
      return {
        ...state,
        houseHasJobWorker: {
          ...state.houseHasJobWorker,
          isLoading: action.payload,
        },
      };
    case FETCH_HOUSE_HAS_JOB_WORKER_SUCCESS:
      return {
        ...state,
        houseHasJobWorker: {
          ...state.houseHasJobWorker,
          isLoading: false,
          data: action.payload,
        },
      };
    case FETCH_HOUSE_HAS_JOB_WORKER_FAILED:
      return {
        ...state,
        houseHasJobWorker: {
          ...state.houseHasJobWorker,
          isLoading: false,
          error: action.payload,
        },
      };
    case TOGGLE_JOB_BOARD_CHECKBOX:
      return toggleJobBoardCheckbox(state, action.payload);
    case TOGGLE_JOB_BOARD_FAV_SUCCESS:
      return {
        ...state,
        houseHasJobWorker: {
          ...state.houseHasJobWorker,
          data: action.payload,
        },
      };
    case TOGGLE_JOB_BOARD_FAV_FAILED:
      return {
        ...state,
        houseHasJobWorker: {
          ...state.houseHasJobWorker,
          error: action.payload,
        },
      };
    case TOGGLE_JOB_BOARD_FAV_PROGRESS:
      return {
        ...state,
        houseHasJobWorker: {
          ...state.houseHasJobWorker,
          isLoading: action.payload,
        },
      };
    case REGISTER_JOBS_SUCCESS:
      return {
        ...state,
        houseHasJobWorker: {
          ...state.houseHasJobWorker,
          data: action.payload,
        },
      };
    case REGISTER_JOBS_FAILED:
      return {
        ...state,
        houseHasJobWorker: {
          ...state.houseHasJobWorker,
          error: action.payload,
        },
      };
    case REGISTER_JOBS_PROGRESS:
      return {
        ...state,
        houseHasJobWorker: {
          ...state.houseHasJobWorker,
          isLoading: action.payload,
        },
      };
    case GET_COMMUNITY_USER_HAS_JOB_SUCCESS:
      return {
        ...state,
        communityUserHasJob: {
          ...state.communityUserHasJob,
          data: action.payload,
        },
      };
    case GET_COMMUNITY_USER_HAS_JOB_FAILED:
      return {
        ...state,
        communityUserHasJob: {
          ...state.communityUserHasJob,
          error: action.payload,
        },
      };
    case GET_COMMUNITY_USER_HAS_JOB_PROGRESS:
      return {
        ...state,
        communityUserHasJob: {
          ...state.communityUserHasJob,
          isLoading: action.payload,
        },
      };
    case CLEAR_JOB_BOARD_STATE:
      return initialState;
    default:
      return state;
  }
}

const toggleJobBoardCheckbox = (state: JobBoardState, payload: string) => {
  const data = state.houseHasJobWorker.data;
  if (!data) return state;

  const currentJob = data.find((item: any) => item.jobId === payload);
  if (!currentJob) return state;

  const isChecking = !currentJob.isChecked;
  const { scheduleActivitytId, allowSignUpSameHouse } = currentJob;

  /**
   *  CASE 1:
   * ang CHECK job mi
   * nhng  c job khc cng scheduleActivitytId
   *  disable job va click
   */
  if (
    isChecking &&
    !allowSignUpSameHouse &&
    scheduleActivitytId &&
    data.some(
      (item: any) =>
        item.jobId !== payload &&
        item.isChecked &&
        item.scheduleActivitytId === scheduleActivitytId,
    )
  ) {
    const updateData = data.map((item: any) =>
      item.jobId === payload ? { ...item, isDisabled: true } : item,
    );

    return {
      ...state,
      houseHasJobWorker: {
        ...state.houseHasJobWorker,
        data: updateData,
      },
    };
  }

  /**
   *  CASE 2:
   * ang UNCHECK job
   *  enable li cc job cng scheduleActivitytId
   */
  if (!isChecking && !allowSignUpSameHouse && scheduleActivitytId) {
    const updateData = data.map((item: any) => {
      // uncheck chnh n
      if (item.jobId === payload) {
        return { ...item, isChecked: false };
      }

      // enable li job cng activity
      if (item.scheduleActivitytId === scheduleActivitytId) {
        return { ...item, isDisabled: false };
      }

      return item;
    });

    return {
      ...state,
      houseHasJobWorker: {
        ...state.houseHasJobWorker,
        data: updateData,
      },
    };
  }

  /**
   *  CASE 3:
   * Toggle bnh thng
   */
  const updateData = data.map((item: any) =>
    item.jobId === payload ? { ...item, isChecked: !item.isChecked } : item,
  );

  return {
    ...state,
    houseHasJobWorker: {
      ...state.houseHasJobWorker,
      data: updateData,
    },
  };
};
