import { AppState } from './types';
import {
  SHOW_ALERT,
  HIDE_ALERT,
  HIDE_LOADING_OVERLAY,
  SHOW_LOADING_OVERLAY,
  SET_UNREAD_MESSAGES,
  SHOW_SELECTED_LANGUAGE_MODAL,
  HIDE_SELECTED_LANGUAGE_MODAL,
  HIDE_CONFIRM_MODAL,
  SHOW_CONFIRM_MODAL,
  LANGUAGE,
  SHOW_VIDEO_MODAL,
  HIDE_VIDEO_MODAL,
  SET_MEDIA,
  HIDE_CUSTOM_MODAL,
  SHOW_CUSTOM_MODAL,
  SET_FIRST_SCREEN_OPEN_APP,
  HIDE_SELECT_BOTTOM_MODAL,
  SHOW_SELECT_BOTTOM_MODAL,
  IS_SHOW_SUGGEST_SETTING_LOGIN_BIOMETRIC_MODAL,
  FETCH_GET_VERSION_APP_SUCCESS,
  FETCH_GET_VERSION_APP_FAILED,
} from './constants';
import { AppAction } from './actions';

const initialState: AppState = {
  alertModal: {
    visible: false,
  },
  loadingOverlay: {
    isVisible: false,
  },
  unreadMessages: 0,
  modalSelectedLanguage: {
    visible: false,
  },

  confirmModal: {
    visible: false,
  },
  language: 'en',
  dataVideoModal: {
    visible: false,
    onClose: () => { },
    source: {},
    subtitle: {},
  },
  dataMedia: null,
  customModal: {
    visible: false,
  },
  firstScreenOpenApp: null,
  selectBottomModal: {
    isVisible: false,
  },
  isShowSuggestSettingLoginBiometricModal: true,
  versionApp: null,
};

export default function counterReducer(
  state = initialState,
  action: AppAction,
): AppState {
  switch (action.type) {
    case SHOW_ALERT:
      return { ...state, alertModal: { ...action.payload, visible: true } };

    case HIDE_ALERT:
      return { ...state, alertModal: { ...state.alertModal, visible: false } };

    case SHOW_LOADING_OVERLAY:
      return {
        ...state,
        loadingOverlay: { ...action.payload, isVisible: true },
      };

    case HIDE_LOADING_OVERLAY:
      return { ...state, loadingOverlay: { isVisible: false } };

    case SET_UNREAD_MESSAGES:
      return { ...state, unreadMessages: action.payload };

    case SHOW_SELECTED_LANGUAGE_MODAL:
      return { ...state, modalSelectedLanguage: { ...action.payload, visible: true } };

    case HIDE_SELECTED_LANGUAGE_MODAL:
      return { ...state, modalSelectedLanguage: { ...state.modalSelectedLanguage, visible: false } };

    case SHOW_CONFIRM_MODAL:
      return { ...state, confirmModal: { ...action.payload, visible: true } };

    case HIDE_CONFIRM_MODAL:
      return {
        ...state,
        confirmModal: { ...state.confirmModal, visible: false },
      };
    case LANGUAGE:
      return { ...state, language: action.payload };

    case SHOW_VIDEO_MODAL:
      return { ...state, dataVideoModal: { ...action.payload, visible: true } };
    case HIDE_VIDEO_MODAL:
      return {
        ...state,
        dataVideoModal: { ...state.dataVideoModal, visible: false },
      };
    case SET_MEDIA:
      return { ...state, dataMedia: action.payload };

    case SHOW_CUSTOM_MODAL:
      return { ...state, customModal: { ...action.payload, visible: true } };
    case HIDE_CUSTOM_MODAL:
      return {
        ...state,
        customModal: { ...state.customModal, visible: false },
      };
    case SET_FIRST_SCREEN_OPEN_APP:
      return { ...state, firstScreenOpenApp: action.payload };

    case SHOW_SELECT_BOTTOM_MODAL:
      return {
        ...state,
        selectBottomModal: { ...action.payload, isVisible: true },
      };

    case HIDE_SELECT_BOTTOM_MODAL:
      return {
        ...state,
        selectBottomModal: { ...state.selectBottomModal, isVisible: false },
      };

    case IS_SHOW_SUGGEST_SETTING_LOGIN_BIOMETRIC_MODAL:
      return {
        ...state,
        isShowSuggestSettingLoginBiometricModal: action.payload,
      };

    case FETCH_GET_VERSION_APP_SUCCESS:
      return { ...state, versionApp: { ...state.versionApp, data: action.payload, isLoading: false, error: null } };
    case FETCH_GET_VERSION_APP_FAILED:
      return { ...state, versionApp: { ...state.versionApp, data: null, isLoading: false, error: action.payload } };

    default:
      return state;
  }
}
