import {
  GET_IMPORTANT_JOBS_FAILED,
  GET_IMPORTANT_JOBS_PROGRESS,
  GET_IMPORTANT_JOBS_SUCCESS,
  GET_MY_JOBS_TODAY_FAILED,
  GET_MY_JOBS_TODAY_PROGRESS,
  GET_MY_JOBS_TODAY_SUCCESS,
  SET_IS_SHOW_DUE_VIDEO,
} from './contants';
import { HomeAction } from './actions';
import { HomeState } from './types';

export const initialState: HomeState = {
  myJobsToday: {
    isLoading: false,
    error: null,
    data: [],
  },
  importantJobs: {
    isLoading: false,
    error: null,
    data: [],
  },
  isShowDueVideo: false,
};

export default function homeReducer(
  state = initialState,
  action: HomeAction,
): HomeState {
  switch (action.type) {
    case GET_MY_JOBS_TODAY_SUCCESS:
      return {
        ...state,
        myJobsToday: {
          isLoading: false,
          error: null,
          data: action.payload,
        },
      };
    case GET_MY_JOBS_TODAY_FAILED:
      return {
        ...state,
        myJobsToday: {
          isLoading: false,
          error: action.payload,
          data: [],
        },
      };
    case GET_MY_JOBS_TODAY_PROGRESS:
      return {
        ...state,
        myJobsToday: {
          ...state.myJobsToday,
          isLoading: action.payload,
        },
      };

    case GET_IMPORTANT_JOBS_SUCCESS:
      return {
        ...state,
        importantJobs: {
          isLoading: false,
          error: null,
          data: action.payload,
        },
      };
    case GET_IMPORTANT_JOBS_FAILED:
      return {
        ...state,
        importantJobs: {
          isLoading: false,
          error: action.payload,
          data: [],
        },
      };
    case GET_IMPORTANT_JOBS_PROGRESS:
      return {
        ...state,
        importantJobs: {
          ...state.importantJobs,
          isLoading: action.payload,
        },
      };
    case SET_IS_SHOW_DUE_VIDEO:
      return {
        ...state,
        isShowDueVideo: action.payload,
      };
    default:
      return state;
  }
}
