import { TrainingAction } from './actions';
import {
  CLEAR_ALL_TUTORIALS,
  CLEAR_MY_TRAININGS,
  GET_ALL_TRAINING_TEST_FAILED,
  GET_ALL_TRAINING_TEST_PROGRESS,
  GET_ALL_TRAINING_TEST_SUCCESS,
  GET_ALL_TUTORIALS_FAILED,
  GET_ALL_TUTORIALS_PROGRESS,
  GET_ALL_TUTORIALS_SUCCESS,
  GET_MY_TRAININGS_FAILED,
  GET_MY_TRAININGS_PROGRESS,
  GET_MY_TRAININGS_SUCCESS,
  MARK_AS_WATCHED_FAILED,
  MARK_AS_WATCHED_PROGRESS,
  MARK_AS_WATCHED_SUCCESS,
  MARK_WATCHED_TRAINING_FAILED,
  MARK_WATCHED_TRAINING_PROGRESS,
  MARK_WATCHED_TRAINING_SUCCESS,
  SAVE_TRAINING_TEST_FAILED,
  SAVE_TRAINING_TEST_PROGRESS,
  SAVE_TRAINING_TEST_SUCCESS,
  UPDATE_TRAINING_FAILED,
  UPDATE_TRAINING_PROGRESS,
  UPDATE_TRAINING_SUCCESS,
  UPDATE_TRAINING_TEST_FAILED,
  UPDATE_TRAINING_TEST_PROGRESS,
  UPDATE_TRAINING_TEST_SUCCESS,
  UPDATE_TUTORIAL_FAILED,
  UPDATE_TUTORIAL_PROGRESS,
  UPDATE_TUTORIAL_SUCCESS,
} from './contants';
import { TrainingState } from './types';

export const initialState: TrainingState = {
  myTrainings: {
    data: [],
    loading: false,
    error: null,
  },
  tutorials: {
    data: [],
    loading: false,
    error: null,
  },
  markAsWatched: {
    data: [],
    loading: false,
    error: null,
  },
  markWatchedTraining: {
    data: [],
    loading: false,
    error: null,
  },
  allTrainingTest: {
    data: [],
    loading: false,
    error: null,
  },
  saveTrainingTest: {
    data: [],
    loading: false,
    error: null,
  },
  loading: false,
};

export default function trainingReducer(
  state = initialState,
  action: TrainingAction,
): TrainingState {
  switch (action.type) {
    case GET_MY_TRAININGS_SUCCESS:
      return {
        ...state,
        myTrainings: {
          data: action.payload,
          loading: false,
          error: null,
        },
      };
    case GET_ALL_TUTORIALS_SUCCESS:
      return {
        ...state,
        tutorials: {
          data: action.payload,
          loading: false,
          error: null,
        },
      };
    case GET_MY_TRAININGS_PROGRESS:
      return {
        ...state,
        myTrainings: {
          ...state.myTrainings,
          loading: action.payload,
        },
      };
    case GET_ALL_TUTORIALS_PROGRESS:
      return {
        ...state,
        tutorials: {
          ...state.tutorials,
          loading: action.payload,
        },
      };
    case CLEAR_MY_TRAININGS:
      return {
        ...state,
        myTrainings: {
          data: [],
          error: null,
          loading: false,
        },
      };
    case CLEAR_ALL_TUTORIALS:
      return {
        ...state,
        tutorials: {
          data: [],
          error: null,
          loading: false,
        },
      };
    case GET_MY_TRAININGS_FAILED:
      return {
        ...state,
        myTrainings: {
          ...state.myTrainings,
          error: action.payload,
          data: [],
        },
      };
    case GET_ALL_TUTORIALS_FAILED:
      return {
        ...state,
        tutorials: {
          ...state.tutorials,
          error: action.payload,
          data: [],
        },
      };

    case UPDATE_TUTORIAL_SUCCESS:
      return {
        ...state,
        tutorials: {
          ...state.tutorials,
          data: action.payload,
          error: null,
        },
      };
    case UPDATE_TUTORIAL_FAILED:
      return {
        ...state,
        tutorials: {
          ...state.tutorials,
          error: action.payload,
        },
      };
    case UPDATE_TUTORIAL_PROGRESS:
      return {
        ...state,
        tutorials: {
          ...state.tutorials,
          loading: action.payload,
        },
      };
    case MARK_AS_WATCHED_SUCCESS:
      return {
        ...state,
        markAsWatched: {
          ...state.markAsWatched,
          data: action.payload,
          error: null,
        },
      };
    case MARK_AS_WATCHED_FAILED:
      return {
        ...state,
        markAsWatched: {
          ...state.markAsWatched,
          error: action.payload,
        },
      };
    case MARK_AS_WATCHED_PROGRESS:
      return {
        ...state,
        markAsWatched: {
          ...state.markAsWatched,
          loading: action.payload,
        },
      };

    case MARK_WATCHED_TRAINING_SUCCESS:
      return {
        ...state,
        markWatchedTraining: {
          ...state.markWatchedTraining,
          data: action.payload,
          error: null,
        },
      };
    case MARK_WATCHED_TRAINING_FAILED:
      return {
        ...state,
        markWatchedTraining: {
          ...state.markWatchedTraining,
          error: action.payload,
        },
      };
    case MARK_WATCHED_TRAINING_PROGRESS:
      return {
        ...state,
        markWatchedTraining: {
          ...state.markWatchedTraining,
          loading: action.payload,
        },
      };
    case UPDATE_TRAINING_SUCCESS:
      return {
        ...state,
        myTrainings: {
          ...state.myTrainings,
          data: action.payload,
          error: null,
        },
      };
    case UPDATE_TRAINING_FAILED:
      return {
        ...state,
        myTrainings: {
          ...state.myTrainings,
          error: action.payload,
        },
      };
    case UPDATE_TRAINING_PROGRESS:
      return {
        ...state,
        myTrainings: {
          ...state.myTrainings,
          loading: action.payload,
        },
      };

    case GET_ALL_TRAINING_TEST_SUCCESS:
      return {
        ...state,
        allTrainingTest: {
          ...state.allTrainingTest,
          data: action.payload,
        },
      };
    case GET_ALL_TRAINING_TEST_FAILED:
      return {
        ...state,
        allTrainingTest: {
          ...state.allTrainingTest,
          error: action.payload,
        },
      };
    case GET_ALL_TRAINING_TEST_PROGRESS:
      return {
        ...state,
        allTrainingTest: {
          ...state.allTrainingTest,
          loading: action.payload,
        },
      };
    case UPDATE_TRAINING_TEST_SUCCESS:
      return {
        ...state,
        allTrainingTest: {
          ...state.allTrainingTest,
          data: action.payload,
        },
      };
    case UPDATE_TRAINING_TEST_FAILED:
      return {
        ...state,
        allTrainingTest: {
          ...state.allTrainingTest,
          error: action.payload,
        },
      };
    case UPDATE_TRAINING_TEST_PROGRESS:
      return {
        ...state,
        allTrainingTest: {
          ...state.allTrainingTest,
          loading: action.payload,
        },
      };
    case SAVE_TRAINING_TEST_SUCCESS:
      return {
        ...state,
        saveTrainingTest: {
          ...state.saveTrainingTest,
          data: action.payload,
        },
      };
    case SAVE_TRAINING_TEST_FAILED:
      return {
        ...state,
        saveTrainingTest: {
          ...state.saveTrainingTest,
          error: action.payload,
        },
      };
    case SAVE_TRAINING_TEST_PROGRESS:
      return {
        ...state,
        saveTrainingTest: {
          ...state.saveTrainingTest,
          loading: action.payload,
        },
      };
    default:
      return state;
  }
}
