import { call, put, takeEvery } from 'redux-saga/effects';
import {
  GET_WORK_ORDER_DETAIL_ASYNC,
  GET_WORK_ORDER_DETAIL_FAILED,
  GET_WORK_ORDER_DETAIL_PROGRESS,
  GET_WORK_ORDER_DETAIL_SUCCESS,
  GET_MATERIAL_LIST_ASYNC,
  GET_MATERIAL_LIST_FAILED,
  GET_MATERIAL_LIST_PROGRESS,
  GET_MATERIAL_LIST_SUCCESS,
  GET_CHAT_GROUP_PROGRESS,
  GET_CHAT_GROUP_FAILED,
  GET_CHAT_GROUP_ASYNC,
  GET_CHAT_GROUP_SUCCESS,
  GET_CHECKLIST_SUCCESS,
  GET_COMMENT_CHECKLIST_PROGRESS,
  GET_COMMENT_CHECKLIST_FAILED,
  GET_COMMENT_CHECKLIST_ASYNC,
  GET_COMMENT_CHECKLIST_SUCCESS,
} from './contants';
import { HIDE_LOADING_OVERLAY, SHOW_LOADING_OVERLAY } from '../app/constants';
import {
  fetchChatGroup,
  fetchComment,
  fetchJobDetail,
} from '../../../api/myjob';
import { fetchMaterialList } from '../../../api/myjob';
import { ResponseApi } from '../../store/types';
import {
  CommentPayload,
  GetChatGroupPayload,
  GetMaterialListPayload,
  GetWorkOrderDetailPayload,
} from './types';

const getWorkOrderDetail = function* (action: {
  type: string;
  payload: GetWorkOrderDetailPayload;
}): Generator<any, void, any> {
  try {
    yield put({ type: GET_WORK_ORDER_DETAIL_PROGRESS, payload: true });
    yield put({ type: SHOW_LOADING_OVERLAY });
    const response = (yield call(
      fetchJobDetail,
      action.payload,
    )) as ResponseApi;
    if (response.status !== 200) {
      yield put({
        type: GET_WORK_ORDER_DETAIL_FAILED,
        payload: response.message,
      });
      return;
    }

    console.log('response.data.checklists', response.data.checklists);

    yield put({
      type: GET_WORK_ORDER_DETAIL_SUCCESS,
      payload: response.data,
    });

    yield put({
      type: GET_CHECKLIST_SUCCESS,
      payload: response.data.checklists,
    });
  } catch (error) {
    yield put({ type: GET_WORK_ORDER_DETAIL_FAILED, payload: error });
  } finally {
    yield put({ type: GET_WORK_ORDER_DETAIL_PROGRESS, payload: false });
    yield put({ type: HIDE_LOADING_OVERLAY });
  }
};

