const getComment = function* (action: {
  type: string;
  payload: CommentPayload;
}): Generator<any, void, any> {
  try {
    yield put({ type: GET_COMMENT_CHECKLIST_PROGRESS, payload: true });
    yield put({ type: SHOW_LOADING_OVERLAY });
    const response = (yield call(fetchComment, action.payload)) as ResponseApi;
    if (response.status !== 200) {
      yield put({
        type: GET_COMMENT_CHECKLIST_FAILED,
        payload: response.message,
      });
      return;
    }
    console.log('response.data', response.data);
    
    yield put({ type: GET_COMMENT_CHECKLIST_SUCCESS, payload: response.data });
  } catch (error) {
    yield put({ type: GET_COMMENT_CHECKLIST_FAILED, payload: error });
  } finally {
    yield put({ type: GET_COMMENT_CHECKLIST_PROGRESS, payload: false });
    yield put({ type: HIDE_LOADING_OVERLAY });
  }
};


export default function* jobDetailSaga() {
  yield takeEvery(GET_WORK_ORDER_DETAIL_ASYNC, getWorkOrderDetail);
  yield takeEvery(GET_MATERIAL_LIST_ASYNC, getMaterialList);
  yield takeEvery(GET_CHAT_GROUP_ASYNC, getChatGroup);  
  yield takeEvery(GET_COMMENT_CHECKLIST_ASYNC, getComment);     
}
