import { logger } from '../utils';
import { client } from './configAxios';
import {
  getHouseHasJobWorker,
  unFavoriteJob as unFavoriteJobEndpoint,
  addFavoriteJob as addFavoriteJobEndpoint,
  registerJobs as registerJobsEndpoint,
  getCommunityUserHasJob as getCommunityUserHasJobEndpoint,
} from './endpoint';

export interface FetchHouseHasJobWorkerParams {
  FullName?: string;
  LotNumber?: number;
  Latitude?: number;
  Longitude?: number;
  CommunityId?: string;
  Radius?: number;
  CompanyId?: string;
  WorkCategoryId?: string;
  State?: string;
  City?: string;
  Country?: string;
  StartDate?: Date;
  EndDate?: Date;
}

export const fetchHouseHasJobWorker = async (
  params: FetchHouseHasJobWorkerParams,
) => {
  try {
    const response = await client.get(getHouseHasJobWorker, { params });
    return {
      status: response.status,
      code: response.status,
      data: response.data?.data,
    };
  } catch (error: any) {
    logger('error fetchHouseHasJobWorker', error);
    return {
      message: error?.response?.data?.message,
      code: error?.code,
      status: error?.status,
    };
  }
};

