import axios, {
  AxiosError,
  AxiosResponse,
  InternalAxiosRequestConfig,
} from 'axios';
import { logger } from '../utils';
import { store } from '../redux/store';
import { refreshAccessToken } from './endpoint';
import { SessionPayload } from '../redux/features/auth/types';
import { LOGIN_SUCCESS } from '../redux/features/auth/constans';
import { hideAlert, showAlert } from '../redux/features/app/actions';
import i18n from '../i18n';
import { logout } from '../redux/features/auth/actions';

let isRefreshing = false;
let refreshSubscribers: ((token: string) => void)[] = [];

export const rootUrl = 'https://dev4.api.tradewize.biz';

export const client = axios.create({
  baseURL: rootUrl,
  timeout: 60000,
  headers: {
    'Content-Type': 'application/json',
    accept: 'text/plain',
  },
});

// modal alert session expired
export const showAlertSessionExpired = () => {
  try {
    setTimeout(() => {
      store.dispatch(
        showAlert({
          type: 'warning',
          visible: true,
          title: i18n.t('WARNING'),
          message: i18n.t('SESSION_EXPIRED'),
          onClose: async () => {
            store.dispatch(logout());
            store.dispatch(hideAlert());
          },
        }),
      );
    }, 350);
  } catch (error) {
    logger('showAlertSessionExpired', error);
  }
};

// refreshToken
async function refreshToken() {
  try {
    const refToken = store.getState()?.auth?.refreshToken ?? '';
    const response = await client.post(`${rootUrl}${refreshAccessToken}`, {
      refreshToken: refToken,
    });

    const payloadSession: SessionPayload = {
      token: response?.data?.data?.token ?? '',
      refreshToken: response?.data?.data?.refreshToken ?? '',
    };

    store.dispatch({
      type: LOGIN_SUCCESS,
      payload: payloadSession,
    });

    if (response?.data?.statusCode !== 200) {
      showAlertSessionExpired();
      return;
    }

    return response.data?.data?.token;
  } catch (error) {
    logger('Refresh token failed:', error);
    showAlertSessionExpired();
  }
}

// Add a request interceptor
client.interceptors.request.use(
  async function (config: InternalAxiosRequestConfig) {
    // Add token if available
    const token = store.getState()?.auth?.token ?? '';
    const lang = store.getState()?.app?.language ?? 'en';
    if (token) {
      config.headers.Authorization = `Bearer ${token}`;
      config.headers['Accept-Language'] = lang;
    } else {
      delete config.headers.Authorization;
    }

    return config;
  },
  function (error: AxiosError) {
    return Promise.reject(error?.message);
  },
);

// Response interceptor
client.interceptors.response.use(
  (response: AxiosResponse) => response,
  async (error: AxiosError) => {
    if (error.response) {
      const { status } = error.response;
      const originalRequest = error.config as {
        headers: { Authorization: string };
        _retry?: boolean;
      };

      if (status === 401 && !originalRequest._retry) {
        const stateRefreshToken = store.getState()?.auth?.refreshToken ?? '';

        if (!stateRefreshToken) {
          isRefreshing = false;
          refreshSubscribers = [];
          showAlertSessionExpired();
          return;
        }

        if (isRefreshing) {
          return new Promise(resolve => {
            refreshSubscribers.push((token: string) => {
              originalRequest.headers.Authorization = `Bearer ${token}`;
              resolve(client(originalRequest));
            });
          });
        }

        originalRequest._retry = true;
        isRefreshing = true;

        try {
          const newToken = await refreshToken();
          isRefreshing = false;
          refreshSubscribers.forEach(callback => callback(newToken));
          refreshSubscribers = [];
          originalRequest.headers.Authorization = `Bearer ${newToken}`;
          return client(originalRequest);
        } catch (err) {
          isRefreshing = false;
          refreshSubscribers = [];
          showAlertSessionExpired();
          return;
        }
      }

      if (status === 402) {
        showAlertSessionExpired();
        return;
      }
    }
    return Promise.reject(error);
  },
);

export default client;
