import { call, put, takeEvery } from 'redux-saga/effects';
import {
  GET_COMPANY_ASYNC,
  GET_COMPANY_FAILED,
  GET_COMPANY_PROGRESS,
  GET_COMPANY_SUCCESS,
} from './contants';
import { ResponseApi } from '../../store/types';
import { fetchRegisterCompany } from '../../../api/companyRegister';
import { HIDE_LOADING_OVERLAY, SHOW_LOADING_OVERLAY } from '../app/constants';

const getCompany = function* (): Generator<any, void, any> {
  try {
    yield put({ type: GET_COMPANY_PROGRESS, payload: true });
    yield put({ type: SHOW_LOADING_OVERLAY });
    const response = (yield call(fetchRegisterCompany)) as ResponseApi;

    if (response.status !== 200) {
      yield put({
        type: GET_COMPANY_FAILED,
        payload: response.message,
      });
      return;
    }

    yield put({ type: GET_COMPANY_SUCCESS, payload: response.data });
  } catch (error) {
  } finally {
    yield put({ type: GET_COMPANY_PROGRESS, payload: false });
    yield put({ type: HIDE_LOADING_OVERLAY });
  }
};

export default function* registerCompanySaga() {
  yield takeEvery(GET_COMPANY_ASYNC, getCompany);
}
