import AsyncStorage from '@react-native-async-storage/async-storage';

export const STORAGE_KEY = {
  USER_LOGIN: '@user_login',
  LANGUAGE: '@app_language',
  BIOMETRIC_KEY: '@biometric_key',
  IS_FIRST_TIME_OPEN_APP: '@is_first_time_open_app',
  VERSION_APP: '@version_app',
};

export const StorageService = {
  // Lu data
  async set(key: string, value: any) {
    try {
      const json = JSON.stringify(value);
      await AsyncStorage.setItem(key, json);
    } catch (err) {
      console.error('Storage set error:', err);
    }
  },

  // Ly data
  async get<T = any>(key: string): Promise<T | null> {
    try {
      const json = await AsyncStorage.getItem(key);
      return json ? JSON.parse(json) : null;
    } catch (err) {
      console.error('Storage get error:', err);
      return null;
    }
  },

  // Xa key
  async delete(key: string) {
    try {
      await AsyncStorage.removeItem(key);
    } catch (err) {
      console.error('Storage delete error:', err);
    }
  },

  // Thay i data (nhn callback  update)
  async update<T = any>(key: string, updater: (oldValue: T | null) => T) {
    try {
      const oldValue = await this.get<T>(key);
      const newValue = updater(oldValue);
      await this.set(key, newValue);
    } catch (err) {
      console.error('Storage update error:', err);
    }
  },
};
