import {
  BANK_INFO_FAILED,
  BANK_INFO_RESET,
  BANK_INFO_SUCCESS,
  SHOW_ACCOUNT_NUMBER_ASYNC,
  SHOW_VERIFY_OTP_ASYNC,
  SHOW_VERIFY_OTP_FAILED,
  SHOW_VERIFY_OTP_RESET,
  SHOW_VERIFY_OTP_SUCCESS,
} from './contants';
import { PaymentInfoW9Action } from './actions';
import { PaymentInfoW9State } from './types';

export const initialState: PaymentInfoW9State = {
  data: null,
  isShowAccountNumber: false,
  isShowVerifyOtpModal: {
    requestId: undefined,
    isShow: false,
    isAddBankAccount: false,
    type: 'email',
  },
};

export default function paymentInfoW9Reducer(
  state = initialState,
  action: PaymentInfoW9Action,
): PaymentInfoW9State {
  switch (action.type) {
    case BANK_INFO_SUCCESS:
      return {
        ...state,
        data: action.payload,
      };
    case BANK_INFO_FAILED:
      return {
        ...state,
        isLoading: false,
        error: action.payload,
        success: false,
      };
    case BANK_INFO_RESET:
      return { ...initialState };

    case SHOW_ACCOUNT_NUMBER_ASYNC: {
      return {
        ...state,
        isShowAccountNumber: action.payload,
      };
    }

    case SHOW_VERIFY_OTP_ASYNC:
      return {
        ...state,
        isShowVerifyOtpModal: {
          ...state.isShowVerifyOtpModal,
          ...action.payload,
        },
      };
    case SHOW_VERIFY_OTP_SUCCESS:
      return {
        ...state,
        isShowVerifyOtpModal: {
          ...state.isShowVerifyOtpModal,
          isShow: true,
        },
      };

    case SHOW_VERIFY_OTP_FAILED:
      return {
        ...state,
        isShowVerifyOtpModal: {
          ...state.isShowVerifyOtpModal,
          isShow: false,
        },
      };

    case SHOW_VERIFY_OTP_RESET:
      return {
        ...state,
        isShowVerifyOtpModal: {
          ...initialState.isShowVerifyOtpModal,
        },
      };

    default:
      return state;
  }
}
