import client from './configAxios';
import {
  getMyTrainings as getMyTrainingsEndpoint,
  getAllTutorials as getAllTutorialsEndpoint,
  markAsWatched as markAsWatchedEndpoint,
  markWatchedTraining as markWatchedTrainingEndpoint,
  getAllTrainingTest as getAllTrainingTestEndpoint,
  saveTrainingTest as saveTrainingTestEndpoint,
} from './endpoint';
import { logger } from '../utils';

export interface SaveTrainingTestPayload {
  questionId: string;
  selectedOptionIds: string[];
  files: string[];
}

export const getMyTrainings = async () => {
  try {
    const response = await client.get(getMyTrainingsEndpoint);
    return {
      status: response.status,
      code: response.status,
      data: response.data?.data,
    };
  } catch (error: any) {
    logger('error getMyTrainings', error);
    return {
      message: error?.response?.data?.message,
      code: error?.code,
      status: error?.status,
    };
  }
};

