export const saveTrainingTest = async (formData: FormData) => {
  try {
    const response = await client.post(saveTrainingTestEndpoint, formData, {
      headers: {
        'Content-Type': 'multipart/form-data',
      },
    });
    return {
      status: response.status,
      code: response.status,
      data: response.data?.data,
    };
  } catch (error: any) {
    logger('error saveTrainingTest', error);
    return {
      message: error?.response?.data?.message,
      code: error?.code,
      status: error?.status,
    };
  }
};
