import languages from '@cospired/i18n-iso-languages';
import i18n from '../i18n';
import client from '../api/configAxios';
import { presignedUrl } from '../api/endpoint';
import { Platform } from 'react-native';
import { check, PERMISSIONS, RESULTS } from 'react-native-permissions';
import Geolocation from 'react-native-geolocation-service';

export const FORMAT_PHONE_NO = 'xxx-xxx-xxxx';
export const FORMAT_ZIP_CODE = 'xxxxx';
export const FORMAT_DATE_OF_BIRTH = 'MM/DD/YYYY';

export const UPDATE_APP_URL_IOS = 'https://apps.apple.com/app/id6743073114';
export const UPDATE_APP_URL_ANDROID = 'market://details?id=com.tradewize';


const supportedCodes = Object.keys(i18n.options.resources || {});

export const logger = (...args: any[]) => {
  if (__DEV__) {
    console.log(...args);
  }
};

// Hm import ng locale tng ng
supportedCodes.forEach(code => {
  try {
    const langData = require(`@cospired/i18n-iso-languages/langs/${code}.json`);
    languages.registerLocale(langData);
  } catch {
    // fallback nu locale khng tn ti trong th vin
    console.warn(`No locale found for: ${code}`);
  }
});

export const getAvailableLanguages = () => {
  const current = i18n.language || 'en';
  return supportedCodes.map(code => ({
    key: code,
    value:
      languages.getName(code, current) ||
      languages.getName(code, 'en') ||
      code.toUpperCase(),
  }));
};

export const generateUUID = (): string => {
  return 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, c => {
    // eslint-disable-next-line no-bitwise
    const r = (Math.random() * 16) | 0;
    // eslint-disable-next-line no-bitwise
    const v = c === 'x' ? r : (r & 0x3) | 0x8;
    return v.toString(16);
  });
};

export const formatPhoneNumber = (input: string): string => {
  const digits = input.replace(/\D/g, '');

  if (digits.length <= 3) return digits;
  if (digits.length <= 6) return `${digits.slice(0, 3)}-${digits.slice(3)}`;

  return `${digits.slice(0, 3)}-${digits.slice(3, 6)}-${digits.slice(6, 10)}`;
};

export const formatMiles = (distance: number) => {
  const formatted = Number(distance).toFixed(1);
  return formatted.endsWith('.0') ? formatted.slice(0, -2) : formatted;
};

export const formatCurrency = (value: number): string => {
  const units = ['', 'K', 'M', 'B', 'T', 'Qa', 'Qi']; // K: 10^3, M: 10^6, ..., Qi: 10^18
  let unitIndex = 0;

  if (value < 1000000000000) {
    return (
      '$' +
      new Intl.NumberFormat('en-US', {
        minimumFractionDigits: 2,
        maximumFractionDigits: 2,
        useGrouping: true,
      }).format(Math.floor(value * 100) / 100)
    ); // Ct b phn tha
  }

  while (value >= 1000 && unitIndex < units.length - 1) {
    value /= 1000;
    unitIndex++;
  }

  // Ct phn l m khng lm trn ln
  const formattedValue = Math.floor(value * 100) / 100;

  return (
    '$' +
    formattedValue.toString().replace(/\.?0+$/, '') +
    ' ' +
    units[unitIndex]
  );
};

export const getFileFromS3Key = async (key: string): Promise<string> => {
  try {
    const encodedKey = encodeURIComponent(key);
    const response = await client.get(`${presignedUrl}/${encodedKey}`);

    if (response && response.status === 200) {
      return response.data?.data;
    }
    return '';
  } catch (error) {
    return '';
  }
};

export const getCurrentLocation = async (): Promise<any> => {
  try {
    const permission =
      Platform.OS === 'ios'
        ? PERMISSIONS.IOS.LOCATION_WHEN_IN_USE
        : PERMISSIONS.ANDROID.ACCESS_FINE_LOCATION;

    const status = await check(permission);

    if (status !== RESULTS.GRANTED) return null;

    return await new Promise(resolve => {
      Geolocation.getCurrentPosition(
        position => {
          resolve({
            latitude: position.coords.latitude,
            longitude: position.coords.longitude,
            timestamp: position.timestamp,
          });
        },
        error => {
          logger('error getCurrentLocation', error);
          resolve(null);
        },
        { enableHighAccuracy: true, timeout: 15000, maximumAge: 10000 },
      );
    });
  } catch (error) {
    logger('error getCurrentLocation', error);
    return null;
  }
};

export const compareVersions = (v1: string, v2: string): number => {
  const a = v1.split('.').map(Number);
  const b = v2.split('.').map(Number);
  for (let i = 0; i < Math.max(a.length, b.length); i++) {
    const n1 = a[i] || 0;
    const n2 = b[i] || 0;
    if (n1 > n2) {
      return 1;
    }
    if (n1 < n2) {
      return -1;
    }
  }
  return 0;
};