import { call, put, takeEvery } from 'redux-saga/effects';
import {
  GET_ROLE_TYPE_ASYNC,
  GET_ROLE_TYPE_FAILED,
  GET_ROLE_TYPE_PROGRESS,
  GET_ROLE_TYPE_SUCCESS,
} from './contants';
import { ResponseApi } from '../../store/types';
import { fetchRoleType } from '../../../api/roleType.ts';
import { HIDE_LOADING_OVERLAY, SHOW_LOADING_OVERLAY } from '../app/constants';

const getRoleType = function* (): Generator<any, void, any> {
  try {
    yield put({ type: GET_ROLE_TYPE_PROGRESS, payload: true });
    yield put({ type: SHOW_LOADING_OVERLAY });
    const response = (yield call(fetchRoleType)) as ResponseApi;

    if (response.status !== 200) {
      yield put({
        type: GET_ROLE_TYPE_FAILED,
        payload: response.message,
      });
      return;
    }

    yield put({ type: GET_ROLE_TYPE_SUCCESS, payload: response.data });
  } catch (error) {
  } finally {
    yield put({ type: GET_ROLE_TYPE_PROGRESS, payload: false });
    yield put({ type: HIDE_LOADING_OVERLAY });
  }
};

export default function* roleTypeSaga() {
  yield takeEvery(GET_ROLE_TYPE_ASYNC, getRoleType);
}
