import { call, delay, put, takeEvery } from 'redux-saga/effects';
import {
  GET_ALL_TRAINING_TEST_ASYNC,
  GET_ALL_TRAINING_TEST_FAILED,
  GET_ALL_TRAINING_TEST_PROGRESS,
  GET_ALL_TRAINING_TEST_SUCCESS,
  GET_ALL_TUTORIALS_ASYNC,
  GET_ALL_TUTORIALS_FAILED,
  GET_ALL_TUTORIALS_PROGRESS,
  GET_ALL_TUTORIALS_SUCCESS,
  GET_MY_TRAININGS_ASYNC,
  GET_MY_TRAININGS_FAILED,
  GET_MY_TRAININGS_PROGRESS,
  GET_MY_TRAININGS_SUCCESS,
  MARK_AS_WATCHED_ASYNC,
  MARK_AS_WATCHED_FAILED,
  MARK_AS_WATCHED_PROGRESS,
  MARK_AS_WATCHED_SUCCESS,
  MARK_WATCHED_TRAINING_ASYNC,
  MARK_WATCHED_TRAINING_FAILED,
  MARK_WATCHED_TRAINING_PROGRESS,
  MARK_WATCHED_TRAINING_SUCCESS,
  SAVE_TRAINING_TEST_ASYNC,
  SAVE_TRAINING_TEST_FAILED,
  SAVE_TRAINING_TEST_PROGRESS,
  SAVE_TRAINING_TEST_SUCCESS,
  UPDATE_ALL_TRAINING_TEST,
  UPDATE_TRAINING_ASYNC,
  UPDATE_TRAINING_FAILED,
  UPDATE_TRAINING_PROGRESS,
  UPDATE_TRAINING_SUCCESS,
  UPDATE_TRAINING_TEST_ASYNC,
  UPDATE_TRAINING_TEST_FAILED,
  UPDATE_TRAINING_TEST_PROGRESS,
  UPDATE_TRAINING_TEST_SUCCESS,
  UPDATE_TUTORIAL_ASYNC,
  UPDATE_TUTORIAL_FAILED,
  UPDATE_TUTORIAL_PROGRESS,
  UPDATE_TUTORIAL_SUCCESS,
} from './contants';
import { ResponseApi } from '../../store/types';
import {
  getAllTrainingTest,
  getAllTutorials,
  getMyTrainings,
  markAsWatched,
  markWatchedTraining,
  saveTrainingTest as saveTrainingTestApi,
} from '../../../api/training';
import { store } from '../..';
import {
  HIDE_LOADING_OVERLAY,
  SHOW_ALERT,
  SHOW_LOADING_OVERLAY,
} from '../app/constants';
import i18n from '../../../i18n';
import colors from '../../../theme/colors';
import { logger } from '../../../utils';

const fetchMyTrainings = function* (): Generator<any, void, any> {
  try {
    yield put({ type: GET_MY_TRAININGS_PROGRESS, payload: true });
    const response = (yield call(getMyTrainings)) as ResponseApi;
    if (response.status !== 200) {
      yield put({ type: GET_MY_TRAININGS_FAILED, payload: response.message });
      return;
    }
    yield put({ type: GET_MY_TRAININGS_SUCCESS, payload: response.data });
  } catch (error) {
    yield put({ type: GET_MY_TRAININGS_FAILED, payload: error });
  } finally {
    yield put({ type: GET_MY_TRAININGS_PROGRESS, payload: false });
  }
};

const fetchAllTutorials = function* (): Generator<any, void, any> {
  try {
    yield put({ type: GET_ALL_TUTORIALS_PROGRESS, payload: true });
    const response = (yield call(getAllTutorials)) as ResponseApi;
    if (response.status !== 200) {
      yield put({ type: GET_ALL_TUTORIALS_FAILED, payload: response.message });
      return;
    }
    yield put({ type: GET_ALL_TUTORIALS_SUCCESS, payload: response.data });
  } catch (error) {
    yield put({ type: GET_ALL_TUTORIALS_FAILED, payload: error });
  } finally {
    yield put({ type: GET_ALL_TUTORIALS_PROGRESS, payload: false });
  }
};

const updateTutorial = function* (action: {
  type: string;
  payload: any;
}): Generator<any, void, any> {
  try {
    yield put({ type: UPDATE_TUTORIAL_PROGRESS, payload: true });

    const tutorial = store.getState().training.tutorials;
    const tutorialData = tutorial?.data?.tutorials || [];
    const newTutorialData = tutorialData?.map((el: any) => {
      if (el?.id === action.payload.id) {
        return { ...el, ...action.payload };
      }
      return el;
    });

    const totalWatched = newTutorialData?.filter(
      (el: any) => el?.isWatched,
    ).length;

    const newTutorial = {
      tutorials: newTutorialData,
      totalWatched: totalWatched,
      totalTutorials: tutorial?.data?.totalTutorials,
    };

    yield put({ type: UPDATE_TUTORIAL_SUCCESS, payload: newTutorial });
  } catch (error) {
    yield put({ type: UPDATE_TUTORIAL_FAILED, payload: error });
  } finally {
    yield put({ type: UPDATE_TUTORIAL_PROGRESS, payload: false });
  }
};

const fetchMarkAsWatched = function* (action: {
  type: string;
  payload: any;
}): Generator<any, void, any> {
  try {
    yield put({ type: MARK_AS_WATCHED_PROGRESS, payload: true });
    const response = (yield call(
      markAsWatched,
      action.payload?.id,
    )) as ResponseApi;
    if (response.status !== 200) {
      yield put({ type: MARK_AS_WATCHED_FAILED, payload: response.message });
      return;
    }
    yield put({ type: MARK_AS_WATCHED_SUCCESS, payload: response.data });
    yield delay(150);
    if (action.payload?.typeQualityGuide) {
      yield call(updateTraining, {
        type: UPDATE_TRAINING_ASYNC,
        payload: {
          id: action.payload?.id,
          isWatched: true,
          isDue: false,
          typeTutorial: true,
        },
      });
    } else {
      yield call(updateTutorial, {
        type: UPDATE_TUTORIAL_ASYNC,
        payload: { id: action.payload?.id, isWatched: true, isDue: false },
      });
    }
  } catch (error) {
    yield put({ type: MARK_AS_WATCHED_FAILED, payload: error });
  } finally {
    yield put({ type: MARK_AS_WATCHED_PROGRESS, payload: false });
  }
};

const updateTraining = function* (action: {
  type: string;
  payload: any;
}): Generator<any, void, any> {
  try {
    yield put({ type: UPDATE_TRAINING_PROGRESS, payload: true });

    const training = store.getState().training.myTrainings;
    if (action.payload?.typeTutorial) {
      const tutorialData = training?.data?.tutorialsByCompany || [];
      const tutorialUpdate = {
        id: action.payload?.id,
        isWatched: action.payload?.isWatched,
        isDue: action.payload?.isDue,
      };
      const newTutorialData = tutorialData?.map((el: any) => ({
        ...el,
        tutorials: (el.tutorials ?? []).map((tutorial: any) =>
          tutorial?.id === action.payload?.id
            ? { ...tutorial, ...tutorialUpdate }
            : tutorial,
        ),
      }));

      const newTutorial = {
        ...training.data,
        tutorialsByCompany: newTutorialData,
      };

      yield put({ type: UPDATE_TRAINING_SUCCESS, payload: newTutorial });
    } else {
      const trainingData = training?.data?.trainings || [];
      const newTrainingData = (trainingData ?? []).map((el: any) => ({
        ...el,
        trainings: (el.trainings ?? []).map((tutorial: any) =>
          tutorial?.id === action.payload.id
            ? { ...tutorial, ...action.payload }
            : tutorial,
        ),
      }));

      const newTraining = {
        ...training.data,
        trainings: newTrainingData,
      };

      yield put({ type: UPDATE_TRAINING_SUCCESS, payload: newTraining });
    }
  } catch (error) {
    yield put({ type: UPDATE_TRAINING_FAILED, payload: error });
  } finally {
    yield put({ type: UPDATE_TRAINING_PROGRESS, payload: false });
  }
};

const fetchMarkWatchedTraining = function* (action: {
  type: string;
  payload: string;
}): Generator<any, void, any> {
  try {
    yield put({ type: MARK_WATCHED_TRAINING_PROGRESS, payload: true });
    const response = (yield call(
      markWatchedTraining,
      action.payload,
    )) as ResponseApi;
    if (response.status !== 200) {
      yield put({
        type: MARK_WATCHED_TRAINING_FAILED,
        payload: response.message,
      });
      return;
    }
    yield put({ type: MARK_WATCHED_TRAINING_SUCCESS, payload: response.data });
    yield delay(150);
    yield call(updateTraining, {
      type: UPDATE_TRAINING_ASYNC,
      payload: { id: action.payload, isWatched: true },
    });
  } catch (error) {
    yield put({ type: MARK_WATCHED_TRAINING_FAILED, payload: error });
  } finally {
    yield put({ type: MARK_WATCHED_TRAINING_PROGRESS, payload: false });
  }
};

const fetchAllTrainingTest = function* (action: {
  type: string;
  payload: string;
}): Generator<any, void, any> {
  try {
    yield put({ type: GET_ALL_TRAINING_TEST_PROGRESS, payload: true });
    yield put({ type: SHOW_LOADING_OVERLAY });
    const response = (yield call(
      getAllTrainingTest,
      action.payload,
    )) as ResponseApi;
    if (response.status !== 200) {
      yield put({
        type: GET_ALL_TRAINING_TEST_FAILED,
        payload: response.message,
      });
      return;
    }
    yield put({ type: GET_ALL_TRAINING_TEST_SUCCESS, payload: response.data });
  } catch (error) {
    yield put({ type: GET_ALL_TRAINING_TEST_FAILED, payload: error });
  } finally {
    yield put({ type: GET_ALL_TRAINING_TEST_PROGRESS, payload: false });
    yield put({ type: HIDE_LOADING_OVERLAY });
  }
};

const updateTrainingTest = function* (action: {
  type: string;
  payload: {
    questionId: string;
    answerIds: string;
    questionAnswerImages: {
      id?: string;
      uri: string;
      filename: string;
      type: string;
      extension: string;
    }[];
    type: 'isChecked' | 'isUploaded' | 'removeImage';
    imageId?: string;
  };
}): Generator<any, void, any> {
  try {
    yield put({ type: UPDATE_TRAINING_TEST_PROGRESS, payload: true });
    const allTrainingTest = store.getState().training.allTrainingTest;
    const allTrainingTestData = allTrainingTest?.data || [];
    if (action.payload.type === 'isChecked') {
      const newAllTrainingTestData = allTrainingTestData?.map((el: any) => {
        if (el?.id === action.payload.questionId) {
          return {
            ...el,
            options: el?.options?.map((option: any) => {
              if (option?.id === action.payload.answerIds) {
                return { ...option, isChecked: !option?.isChecked };
              }
              return { ...option, isChecked: false };
            }),
          };
        }
        return el;
      });

      yield put({
        type: UPDATE_TRAINING_TEST_SUCCESS,
        payload: newAllTrainingTestData,
      });
    }

    if (action.payload.type === 'isUploaded') {
      const newAllTrainingTestData = allTrainingTestData?.map((el: any) => {
        if (el?.id === action.payload.questionId) {
          return {
            ...el,
            questionAnswerImages: [
              ...(el?.questionAnswerImages || []),
              ...action.payload.questionAnswerImages,
            ],
          };
        }
        return el;
      });

      yield put({
        type: UPDATE_TRAINING_TEST_SUCCESS,
        payload: newAllTrainingTestData,
      });
    }

    if (action.payload.type === 'removeImage') {
      const newAllTrainingTestData = allTrainingTestData?.map((el: any) => {
        if (el?.id === action.payload.questionId) {
          return {
            ...el,
            questionAnswerImages: el?.questionAnswerImages?.filter(
              (image: any) => image?.id !== action.payload.imageId,
            ),
          };
        }
        return el;
      });
      yield put({
        type: UPDATE_TRAINING_TEST_SUCCESS,
        payload: newAllTrainingTestData,
      });
    }
  } catch (error) {
    yield put({ type: UPDATE_TRAINING_TEST_FAILED, payload: error });
  } finally {
    yield put({ type: UPDATE_TRAINING_TEST_PROGRESS, payload: false });
  }
};

const saveTrainingTest = function* (action: {
  type: string;
  payload: {
    formData: FormData;
    navigationService?: any;
    companyId?: string;
    trainingId?: string;
  };
}): Generator<any, void, any> {
  try {
    const training = store.getState().training.myTrainings;
    const trainingData = training?.data?.trainings || [];
    yield put({ type: SAVE_TRAINING_TEST_PROGRESS, payload: true });
    const response = (yield call(
      saveTrainingTestApi,
      action.payload.formData,
    )) as ResponseApi;
    if (response.status !== 200) {
      yield put({ type: SAVE_TRAINING_TEST_FAILED, payload: response.message });
      return;
    }

    if (response?.data?.isPassed) {
      const newTrainingData = trainingData?.map((el: any) => {
        if (el?.companyId === action.payload.companyId) {
          return {
            ...el,
            trainings: (el?.trainings ?? []).map((item: any) => {
              if (item?.id === action.payload.trainingId) {
                return {
                  ...item,
                  isPassed: true,
                };
              }
              return item;
            }),
          };
        }
        return el;
      });

      const newTraining = {
        ...training.data,
        trainings: newTrainingData,
      };
      yield put({ type: UPDATE_TRAINING_SUCCESS, payload: newTraining });

      yield put({
        type: SHOW_ALERT,
        payload: {
          title: i18n.t('TEST_PASSED'),
          arrayMessage: [
            `${i18n.t('RESULT')}: ${Math.floor(
              (response.data?.correctAnswers / response.data?.totalQuestions) *
                100,
            )}%`,
            `${i18n.t('TOTAL_QUESTIONS_NUMBER', {
              number: response.data?.totalQuestions,
            })}`,
            `${i18n.t('WRONG_ANSWERS_NUMBER', {
              number: response.data?.incorrectAnswers,
            })}`,
            `${i18n.t('RIGHT_ANSWERS_NUMBER', {
              number: response.data?.correctAnswers,
            })}`,
            `${i18n.t('REMAINING_TESTS_TO_UNLOCK_THE_JOB_BOARD_NUMBER', {
              number: response.data?.minPassScore,
            })}`,
          ],
          titleStyle: {
            width: '100%',
            backgroundColor: colors.success,
            borderRadius: 8,
            paddingVertical: 8,
            fontSize: 16,
            fontWeight: '600',
            lineHeight: 24,
            textAlign: 'center',
            color: colors.white,
          },
          buttonStyle: {
            backgroundColor: colors.mainColorPrimary,
          },
          buttonText: i18n.t('CONTINUE_TRAINING'),
          isShowIcon: false,
          type: 'success',
          onClose: () => {
            setTimeout(() => {
              action.payload.navigationService.goBack();
            }, 350);
          },
        },
      });
    } else {
      yield put({
        type: SHOW_ALERT,
        payload: {
          title: i18n.t('TEST_FAILED'),
          arrayMessage: [
            `${i18n.t('RESULT')}: ${Math.floor(
              (response.data?.correctAnswers / response.data?.totalQuestions) *
                100,
            )}%`,
            `${i18n.t('TOTAL_QUESTIONS_NUMBER', {
              number: response.data?.totalQuestions,
            })}`,
            `${i18n.t('WRONG_ANSWERS_NUMBER', {
              number: response.data?.incorrectAnswers,
            })}`,
            `${i18n.t('RIGHT_ANSWERS_NUMBER', {
              number: response.data?.correctAnswers,
            })}`,
          ],
          titleStyle: {
            width: '100%',
            backgroundColor: colors.alertColorError,
            borderRadius: 8,
            paddingVertical: 8,
            fontSize: 16,
            fontWeight: '600',
            lineHeight: 24,
            textAlign: 'center',
            color: colors.white,
          },
          buttonStyle: {
            backgroundColor: colors.mainColorPrimary,
          },
          buttonText: i18n.t('RETAKE_THE_TEST'),
          isShowIcon: false,
          type: 'success',
        },
      });
    }

    yield put({ type: SAVE_TRAINING_TEST_SUCCESS, payload: response.data });
  } catch (error) {
    yield put({ type: SAVE_TRAINING_TEST_FAILED, payload: error });
  } finally {
    yield put({ type: SAVE_TRAINING_TEST_PROGRESS, payload: false });
  }
};

export const updateAllTrainingTest = function* (action: {
  type: string;
  payload: any;
}): Generator<any, void, any> {
  try {
    yield put({
      type: UPDATE_TRAINING_TEST_SUCCESS,
      payload: action.payload,
    });
  } catch (error) {
    logger(error);
  }
};

export default function* trainingSaga() {
  yield takeEvery(GET_MY_TRAININGS_ASYNC, fetchMyTrainings);
  yield takeEvery(GET_ALL_TUTORIALS_ASYNC, fetchAllTutorials);
  yield takeEvery(UPDATE_TUTORIAL_ASYNC, updateTutorial);
  yield takeEvery(MARK_AS_WATCHED_ASYNC, fetchMarkAsWatched);
  yield takeEvery(MARK_WATCHED_TRAINING_ASYNC, fetchMarkWatchedTraining);
  yield takeEvery(UPDATE_TRAINING_ASYNC, updateTraining);
  yield takeEvery(GET_ALL_TRAINING_TEST_ASYNC, fetchAllTrainingTest);
  yield takeEvery(UPDATE_TRAINING_TEST_ASYNC, updateTrainingTest);
  yield takeEvery(SAVE_TRAINING_TEST_ASYNC, saveTrainingTest);
  yield takeEvery(UPDATE_ALL_TRAINING_TEST, updateAllTrainingTest);
}
