import { GET_ROLE_TYPE_PROGRESS, GET_ROLE_TYPE_SUCCESS } from './contants';
import { RoleTypeAction } from './actions';
import { RoleTypeState } from './types';

export const initialState: RoleTypeState = {
  data: [],
  loading: false,
};

export default function referFriendReducer(
  state = initialState,
  action: RoleTypeAction,
): RoleTypeState {
  switch (action.type) {
    case GET_ROLE_TYPE_SUCCESS:
      return {
        ...state,
        data: action.payload,
      };
    case GET_ROLE_TYPE_PROGRESS:
      return {
        ...state,
        loading: action.payload,
      };

    default:
      return state;
  }
}
