import { client } from './configAxios';
import { logger } from '../utils';
import {
  addBankAccountRequest,
  getInfoBankAccount,
  getInfoBankAccountDetail,
  sendOtpEmail,
  sendOtpPhone,
  sendTokenVerifyOtp,
  verifyOtpEmail,
  verifyOtpPhone,
  verifyTokenVerifyOtp,
} from './endpoint';
import {
  SendOtpBankAccountPayload,
  VerifyOtpBankAccountPayload,
} from '../redux/features/paymentInfoW9/types';

export const fetchPaymentInfoW9 = async (token?: string) => {
  try {
    const url = token
      ? `${getInfoBankAccountDetail}?token=${token}`
      : getInfoBankAccount;

    const response = await client.get(url);

    return {
      status: response.status,
      code: response.status,
      data: response.data?.data,
    };
  } catch (error: any) {
    logger('error fetchPaymentInfoW9', error);
    return {
      message: error?.response?.data?.message,
      code: error?.code,
      status: error?.status,
    };
  }
};

