export const fetchSendOtpBankAccount = async (
  payload: SendOtpBankAccountPayload,
) => {
  try {
    const isPhone = payload.type === 'phone';
    const response = await client.post(
      `${isPhone ? sendOtpPhone : sendOtpEmail}?requestId=${payload.requestId}`,
    );

    return {
      status: response.status,
      code: response.status,
      data: response.data?.data,
    };
  } catch (error: any) {
    logger('error fetchSendOtpBankAccount', error);
    return {
      message: error?.response?.data?.message,
      code: error?.code,
      status: error?.status,
    };
  }
};

export const fetchVerifyOtpBankAccount = async (
  payload: VerifyOtpBankAccountPayload,
) => {
  try {
    const isVerifyingPhone = payload.type === 'phone';
    const response = await client.post(
      `${isVerifyingPhone ? verifyOtpPhone : verifyOtpEmail}?requestId=${
        payload.requestId
      }&${isVerifyingPhone ? 'phoneOtp' : 'emailOtp'}=${payload.otp}`,
    );

    return {
      status: response.status,
      code: response.status,
      data: response.data?.data,
    };
  } catch (error: any) {
    logger('error fetchVerifyOtpBankAccount', error);
    return {
      message: error?.response?.data?.message,
      code: error?.code,
      status: error?.status,
    };
  }
};
