import { logger } from '../utils';
import { client } from './configAxios';
import { getReviews, getUserNetwork } from './endpoint';

export const fetchNetwork = async ({
  keyword,
  page = 1,
}: {
  keyword?: string;
  page?: number;
}) => {
  try {
    let url = '';

    if (keyword) {
      url = `${getUserNetwork}?PageIndex=${page}&PageSize=10&FullName=${encodeURIComponent(
        keyword,
      )}`;
    } else {
      url = `${getUserNetwork}?PageIndex=${page}&PageSize=10`;
    }

    const response = await client.get(url);
    return {
      status: response.status,
      code: response.status,
      data: response.data?.data,
    };
  } catch (error: any) {
    logger('error fetchNetwork', error);
    return {
      message: error?.response?.data?.message,
      code: error?.code,
      status: error?.status,
    };
  }
};

export const fetchReviews = async ({ userId }: { userId: string }) => {
  try {
    const response = await client.get(`${getReviews}?userId=${userId}`);
    return {
      status: response.status,
      code: response.status,
      data: response.data?.data,
    };
  } catch (error: any) {
    logger('error fetchReviews', error);
    return {
      message: error?.response?.data?.message,
      code: error?.code,
      status: error?.status,
    };
  }
};
