import { MessageAction } from './actions';
import {
  CLEAR_MESSAGES_BY_ROOM_ID,
  CLEAR_MY_ROOMS,
  GET_MESSAGES_BY_ROOM_ID_FAILED,
  GET_MESSAGES_BY_ROOM_ID_PROGRESS,
  GET_MESSAGES_BY_ROOM_ID_SUCCESS,
  GET_MY_ROOMS_FAILED,
  GET_MY_ROOMS_PROGRESS,
  GET_MY_ROOMS_SUCCESS,
  GET_UNREAD_MESSAGE_FAILED,
  GET_UNREAD_MESSAGE_PROGRESS,
  GET_UNREAD_MESSAGE_SUCCESS,
} from './constants';
import { MessageState } from './types';

const initialState: MessageState = {
  myRooms: {
    isLoading: false,
    error: null,
    data: null,
  },
  messagesByRoomId: {
    isLoading: false,
    error: null,
    data: null,
  },
  unreadMessage: {
    isLoading: false,
    error: null,
    data: null,
  },
};

export const messageReducer = (state = initialState, action: MessageAction) => {
  switch (action.type) {
    case GET_MY_ROOMS_SUCCESS:
      return {
        ...state,
        myRooms: { ...state.myRooms, isLoading: false, data: action.payload },
      };
    case GET_MY_ROOMS_FAILED:
      return {
        ...state,
        myRooms: { ...state.myRooms, isLoading: false, error: action.payload },
      };
    case GET_MY_ROOMS_PROGRESS:
      return {
        ...state,
        myRooms: { ...state.myRooms, isLoading: action.payload },
      };
    case CLEAR_MY_ROOMS:
      return {
        ...state,
        myRooms: initialState.myRooms,
      };
    case GET_MESSAGES_BY_ROOM_ID_SUCCESS:
      return {
        ...state,
        messagesByRoomId: {
          ...state.messagesByRoomId,
          isLoading: false,
          data: action.payload,
        },
      };
    case GET_MESSAGES_BY_ROOM_ID_FAILED:
      return {
        ...state,
        messagesByRoomId: {
          ...state.messagesByRoomId,
          isLoading: false,
          error: action.payload,
        },
      };
    case GET_MESSAGES_BY_ROOM_ID_PROGRESS:
      return {
        ...state,
        messagesByRoomId: {
          ...state.messagesByRoomId,
          isLoading: action.payload,
        },
      };
    case CLEAR_MESSAGES_BY_ROOM_ID:
      return {
        ...state,
        messagesByRoomId: initialState.messagesByRoomId,
      };
    case GET_UNREAD_MESSAGE_SUCCESS:
      return {
        ...state,
        unreadMessage: {
          ...state.unreadMessage,
          isLoading: false,
          data: action.payload,
        },
      };
    case GET_UNREAD_MESSAGE_FAILED:
      return {
        ...state,
        unreadMessage: {
          ...state.unreadMessage,
          isLoading: false,
          error: action.payload,
        },
      };
    case GET_UNREAD_MESSAGE_PROGRESS:
      return {
        ...state,
        unreadMessage: {
          ...state.unreadMessage,
          isLoading: action.payload,
        },
      };
    default:
      return state;
  }
};

export default messageReducer;
