import { logger } from '../utils';
import { client } from './configAxios';
import { registerCompany } from './endpoint';
import { ResponseApi } from '../redux/store/types';

export const fetchRegisterCompany = async () => {
  try {
    const response = (await client.get(registerCompany)) as ResponseApi;
    return {
      status: response.data.statusCode,
      code: response.data.code,
      data: response.data.data,
    };
  } catch (error: any) {
    logger('error fetchReferFriend', error);
    return {
      message: error?.response?.data?.message,
      code: error?.code,
      status: error?.status,
    };
  }
};
