import { AuthState } from './types';
import { AuthAction } from './actions';
import {
  CLEAR_BIOMETRIC_AUTHENTICATION_SUCCESS,
  IS_LOGIN_BIOMETRIC_AUTHENTICATION,
  LOGIN_BIOMETRIC_FAILED,
  LOGIN_BIOMETRIC_SUCCESS,
  LOGIN_FAILED,
  LOGIN_SUCCESS,
  LOGOUT,
  REGISTER_BIOMETRIC_AUTHENTICATION_FAILED,
  REGISTER_BIOMETRIC_AUTHENTICATION_SUCCESS,
  SET_BIOMETRIC_AUTHENTICATION,
  SET_NOTIFICATION_TOKEN,
  SET_USER_PROFILE,
  UPDATE_USER_PROFILE,
} from './constans';

const initialState: AuthState = {
  isLoading: false,
  error: null,
  token: null,
  refreshToken: null,
  userProfile: null,
  fcmToken: null,
  biometricAuthentication: null,
  isLoginBiometricAuthentication: true,
};

export default function counterReducer(
  state = initialState,
  action: AuthAction,
): AuthState {
  switch (action.type) {
    case LOGIN_FAILED:
    case LOGIN_BIOMETRIC_FAILED:
      return { ...state, isLoading: false, error: action.payload };
    case LOGIN_SUCCESS:
    case LOGIN_BIOMETRIC_SUCCESS:
      return {
        ...state,
        isLoading: false,
        token: action.payload.token,
        refreshToken: action.payload.refreshToken,
        error: null,
      };
    case SET_USER_PROFILE:
      return { ...state, userProfile: action.payload };
    case LOGOUT:
      return {
        ...state,
        token: null,
        refreshToken: null,
        userProfile: null,
        isLoginBiometricAuthentication: false,
      };

    case SET_NOTIFICATION_TOKEN:
      return { ...state, fcmToken: action.payload };

    case UPDATE_USER_PROFILE:
      return {
        ...state,
        userProfile: { ...state.userProfile, ...action.payload },
      };

    case SET_BIOMETRIC_AUTHENTICATION:
      return {
        ...state,
        biometricAuthentication: {
          error: null,
          data: action.payload,
          isLoading: false,
        },
      };

    case REGISTER_BIOMETRIC_AUTHENTICATION_FAILED:
      return {
        ...state,
        error: action.payload,
        biometricAuthentication: {
          isLoading: false,
          error: action.payload,
          data: false,
        },
      };
    case REGISTER_BIOMETRIC_AUTHENTICATION_SUCCESS:
      return {
        ...state,
        biometricAuthentication: {
          isLoading: false,
          error: null,
          data: true,
        },
      };
    case IS_LOGIN_BIOMETRIC_AUTHENTICATION:
      return {
        ...state,
        isLoginBiometricAuthentication: action.payload,
      };
    case CLEAR_BIOMETRIC_AUTHENTICATION_SUCCESS:
      return {
        ...state,
        biometricAuthentication: null,
      };

    default:
      return state;
  }
}
