import { VerifyEmailOtpPayload } from '../redux/features/verifyOtp/types';
import { client } from './configAxios';
import { logger } from '../utils';
import { verifyOTP } from './endpoint';

export const fetchVerifyEmailOtp = async (payload: VerifyEmailOtpPayload) => {
  try {
    const response = await client.post(verifyOTP, payload);

    return {
      status: response.status,
      code: response.status,
      data: response.data?.data,
    };
  } catch (error: any) {
    logger('error fetchVerifyOtp', error);
    return {
      message: error?.response?.data?.message,
      code: error?.code,
      status: error?.status,
    };
  }
};
