import { call, delay, put, takeEvery } from 'redux-saga/effects';
import { logger } from '../../../utils';
import { HIDE_LOADING_OVERLAY, SHOW_LOADING_OVERLAY } from '../app/constants';
import {
  BANK_INFO_ASYNC,
  BANK_INFO_FAILED,
  BANK_INFO_SUCCESS,
  SEND_TOKEN_VERIFY_OTP_ASYNC,
  VERIFY_TOKEN_VERIFY_OTP_ASYNC,
  ADD_BANK_ACCOUNT_ASYNC,
  SHOW_ACCOUNT_NUMBER_ASYNC,
  SEND_OTP_BANK_ACCOUNT_ASYNC,
  VERIFY_OTP_BANK_ACCOUNT_ASYNC,
  SHOW_VERIFY_OTP_ASYNC,
  SHOW_VERIFY_OTP_RESET,
} from './contants';
import { ResponseApi } from '../../store/types';
import i18n from '../../../i18n';
import {
  fetchPaymentInfoW9,
  fetchSendTokenVerifyOtp,
  fetchVerifyTokenVerifyOtp,
  fetchAddBankAccount,
  fetchSendOtpBankAccount,
  fetchVerifyOtpBankAccount,
} from '../../../api/paymentInfoW9';
import Toast from 'react-native-toast-message';
import {
  AddBankAccountPayload,
  SendOtpBankAccountPayload,
  VerifyOtpBankAccountPayload,
} from './types';

function* handleBankInfoAsync(action: {
  type: string;
  payload: string;
}): Generator<any, void, any> {
  try {
    yield put({ type: BANK_INFO_FAILED, payload: null });
    yield put({ type: SHOW_LOADING_OVERLAY });

    const response = (yield call(
      fetchPaymentInfoW9,
      action.payload,
    )) as ResponseApi;

    if (response.status === 200) {
      yield put({ type: BANK_INFO_SUCCESS, payload: response.data });
    } else {
      yield put({ type: BANK_INFO_FAILED, payload: response.message });
    }
  } catch (error) {
    logger('error handleBankInfoAsync', error);
    yield put({
      type: BANK_INFO_FAILED,
      payload: i18n.t('BANK_INFO_FAILED'),
    });
  } finally {
    yield put({ type: HIDE_LOADING_OVERLAY });
  }
}

function* handleSendTokenVerifyOtpAsync(): Generator<any, void, any> {
  try {
    yield put({ type: SHOW_LOADING_OVERLAY });

    const response = (yield call(fetchSendTokenVerifyOtp)) as ResponseApi;
    if (response.status === 200) {
      yield call(Toast.show, {
        text1: i18n.t('SEND_TOKEN_VERIFY_OTP_SUCCESS'),
        type: 'success',
      });
      yield put({ type: SHOW_VERIFY_OTP_ASYNC, payload: { isShow: true } });
    } else {
      yield call(Toast.show, {
        text1: i18n.t('SEND_TOKEN_VERIFY_OTP_FAILED'),
        type: 'error',
      });
    }
  } catch (error) {
    logger('error handleVerifyTokenVerifyOtpAsync', error);
    yield call(Toast.show, {
      text1: i18n.t('SEND_TOKEN_VERIFY_OTP_FAILED'),
      type: 'error',
    });
  } finally {
    yield put({ type: HIDE_LOADING_OVERLAY });
  }
}

function* handleVerifyTokenVerifyOtpAsync(action: {
  type: string;
  payload: string;
}): Generator<any, void, any> {
  try {
    yield put({ type: SHOW_LOADING_OVERLAY });

    const response = (yield call(
      fetchVerifyTokenVerifyOtp,
      action.payload,
    )) as ResponseApi;
    if (response.status === 200) {
      yield call(Toast.show, {
        text1: i18n.t('VERIFY_TOKEN_VERIFY_OTP_SUCCESS'),
        type: 'success',
      });
      yield put({ type: SHOW_VERIFY_OTP_RESET });
      yield put({ type: SHOW_ACCOUNT_NUMBER_ASYNC, payload: true });
      yield call(handleBankInfoAsync, {
        type: BANK_INFO_ASYNC,
        payload: response?.data?.token || '',
      });
    } else {
      yield call(Toast.show, {
        text1: i18n.t('VERIFY_TOKEN_VERIFY_OTP_FAILED'),
        type: 'error',
      });
    }
  } catch (error) {
    logger('error handleVerifyTokenVerifyOtpAsync', error);
    yield call(Toast.show, {
      text1: i18n.t('VERIFY_TOKEN_VERIFY_OTP_FAILED'),
      type: 'error',
    });
  } finally {
    yield put({ type: HIDE_LOADING_OVERLAY });
  }
}

function* handleAddBankAccountAsync(action: {
  type: string;
  payload: AddBankAccountPayload;
}): Generator<any, void, any> {
  try {
    yield put({ type: SHOW_LOADING_OVERLAY });

    const response = (yield call(
      fetchAddBankAccount,
      action.payload,
    )) as ResponseApi;
    if (response.status === 200) {
      console.log('response', response);
      yield call(handleSendOtpAccountAsync, {
        type: SEND_OTP_BANK_ACCOUNT_ASYNC,
        payload: {
          requestId: response?.data,
          type: 'phone',
        },
      });
    } else {
      yield call(Toast.show, {
        text1: i18n.t('ADD_BANK_ACCOUNT_REQUEST_FAILED'),
        type: 'error',
      });
    }
  } catch (error) {
    logger('error handleAddBankAccountAsync', error);
    yield call(Toast.show, {
      text1: i18n.t('ADD_BANK_ACCOUNT_FAILED'),
      type: 'error',
    });
  } finally {
    yield put({ type: HIDE_LOADING_OVERLAY });
  }
}

function* handleSendOtpAccountAsync(action: {
  type: string;
  payload: SendOtpBankAccountPayload;
}): Generator<any, void, any> {
  try {
    yield put({ type: SHOW_LOADING_OVERLAY });

    const isPhone = action.payload.type === 'phone';

    const response = (yield call(
      fetchSendOtpBankAccount,
      action.payload,
    )) as ResponseApi;
    if (response.status === 200) {
      yield call(Toast.show, {
        text1: i18n.t(
          `OTP_SENT_SUCCESSFULLY_TO_${isPhone ? 'PHONE' : 'EMAIL'}`,
        ),
        type: 'success',
      });
      yield put({
        type: SHOW_VERIFY_OTP_ASYNC,
        payload: {
          isShow: true,
          isAddBankAccount: true,
          requestId: action.payload?.requestId,
          type: action.payload?.type,
        },
      });
    } else {
      yield call(Toast.show, {
        text1: i18n.t(`OTP_SENT_FAILED_TO_${isPhone ? 'PHONE' : 'EMAIL'}`),
        type: 'error',
      });
    }
  } catch (error) {
    logger('error handleSendOtpAccountAsync', error);
    yield call(Toast.show, {
      text1: i18n.t('SEND_OTP_ACCOUNT_FAILED'),
      type: 'error',
    });
  } finally {
    yield put({ type: HIDE_LOADING_OVERLAY });
  }
}

function* handleVerifyOtpAccountAsync(action: {
  type: string;
  payload: VerifyOtpBankAccountPayload;
}): Generator<any, void, any> {
  logger('action.payload in handleVerifyOtpAccountAsync', action.payload);
  try {
    yield put({ type: SHOW_LOADING_OVERLAY });
    const isPhone = action?.payload?.type === 'phone';

    const response = (yield call(
      fetchVerifyOtpBankAccount,
      action.payload,
    )) as ResponseApi;
    if (response.status === 200) {
      yield call(Toast.show, {
        text1: i18n.t(
          `OTP_VERIFIED_SUCCESSFULLY_TO_${isPhone ? 'PHONE' : 'EMAIL'}`,
        ),
        type: 'success',
      });
      if (isPhone) {
        yield call(delay, 200);
        yield call(handleSendOtpAccountAsync, {
          type: SEND_OTP_BANK_ACCOUNT_ASYNC,
          payload: {
            requestId: action.payload?.requestId,
            type: 'email',
          },
        });
        return;
      } else {
        // Fetch bank info without token to refresh data
        try {
          yield put({ type: SHOW_VERIFY_OTP_RESET });
          yield call(handleBankInfoAsync, {
            type: BANK_INFO_ASYNC,
            payload: '',
          });
        } catch (error) {
          logger(
            'error handleBankInfoAsync in handleVerifyOtpAccountAsync',
            error,
          );
        }
        // Navigate back - similar to register saga
        if (action.payload?.navigationRef) {
          yield call(action.payload.navigationRef.goBack);
        }
        return;
      }
    } else {
      yield call(Toast.show, {
        text1: i18n.t(`OTP_VERIFIED_FAILED_TO_${isPhone ? 'PHONE' : 'EMAIL'}`),
        type: 'error',
      });
    }
  } catch (error) {
    logger('error handleVerifyOtpAccountAsync', error);
    yield call(Toast.show, {
      text1: i18n.t(`OTP_VERIFIED_FAILED`),
      type: 'error',
    });
  } finally {
    yield put({ type: HIDE_LOADING_OVERLAY });
  }
}

export default function* paymentInfoW9Saga() {
  yield takeEvery(BANK_INFO_ASYNC, handleBankInfoAsync);
  yield takeEvery(SEND_TOKEN_VERIFY_OTP_ASYNC, handleSendTokenVerifyOtpAsync);
  yield takeEvery(
    VERIFY_TOKEN_VERIFY_OTP_ASYNC,
    handleVerifyTokenVerifyOtpAsync,
  );
  yield takeEvery(ADD_BANK_ACCOUNT_ASYNC, handleAddBankAccountAsync);
  yield takeEvery(SEND_OTP_BANK_ACCOUNT_ASYNC, handleSendOtpAccountAsync);
  yield takeEvery(VERIFY_OTP_BANK_ACCOUNT_ASYNC, handleVerifyOtpAccountAsync);
}
