import { RegisterAction } from './actions';
import {
  GET_COMPANY_DROPDOWN_FAILED,
  GET_COMPANY_DROPDOWN_SUCCESS,
  GET_STATES_FAILED,
  GET_STATES_PROGRESS,
  GET_STATES_SUCCESS,
  CREATE_INFORMATION_SUCCESS,
  CREATE_INFORMATION_FAILED,
  SAVE_YOUR_EXPERIENCE_FAILED,
  SAVE_YOUR_EXPERIENCE_SUCCESS,
  SAVE_YOUR_EXPERIENCE_REQUESTED,
  CLEAR_STATE_REGISTER_ASYNC,
  SAVE_WORK_REFERENCE_REQUESTED,
  SAVE_WORK_REFERENCE_FAILED,
  SAVE_WORK_REFERENCE_SUCCESS,
  GET_ONBOARDING_QUESTION_FAILED,
  GET_ONBOARDING_QUESTION_SUCCESS,
  GET_ONBOARDING_QUESTION_PROGRESS,
  TOGGLE_COMPANY_CHECKBOX,
  TOGGLE_QUESTION_ANSWER,
  CLEAR_QUESTION_ANSWERS,
  ADD_QUESTION_ANSWER_IMAGES,
  REMOVE_QUESTION_ANSWER_IMAGE,
  HIDE_DATE_PICKER_MODAl,
  SHOW_DATE_PICKER_MODAL,
  SET_DATE_BIRTH_OF_USER,
  CLEAR_ERROR_QUESTION,
  SET_ERROR_QUESTION,
  SAVE_YOUR_TEST_SUCCESS,
  SAVE_YOUR_TEST_FAILED,
  SEND_OTP_VERIFY_ACCOUNT_FAILED,
  SEND_OTP_VERIFY_ACCOUNT_SUCCESS,
  VERIFY_OTP_ACCOUNT_SUCCESS,
  VERIFY_OTP_ACCOUNT_FAILED,
  GET_ONBOARDING_STATUS_SUCCESS,
  GET_ONBOARDING_STATUS_FAILED,
  GET_ONBOARDING_STATUS_PROGRESS,
  CLEAR_ONBOARDING_STATUS,
} from './contains';
import { RegisterState, StateData } from './types';

const initialState: RegisterState = {
  getCompanyDropdown: {
    isLoading: false,
    error: null,
    data: null,
  },
  getStates: {
    isLoading: false,
    error: null,
    data: [],
  },
  createInformation: {
    isLoading: false,
    error: null,
    data: null,
  },
  saveYourExperience: {
    isLoading: false,
    error: null,
    data: null,
    formRequested: null,
  },
  saveWorkReference: {
    isLoading: false,
    error: null,
    data: null,
    formRequested: null,
  },
  getAllOnboardingQuestion: {
    isLoading: false,
    error: null,
    data: null,
  },
  questionAnswers: [],
  datePickerModal: {
    isVisible: false,
    date: '',
  },
  errorQuestion: [],
  saveYourTest: {
    isLoading: false,
    error: null,
    data: null,
    formRequested: null,
  },
  verifyOtpAccount: {
    verifyPhone: {
      isLoading: false,
      error: null,
      data: null,
    },
    verifyEmail: {
      isLoading: false,
      error: null,
      data: null,
    },
  },
  sendOTPVerifyAccount: {
    verifyPhone: {
      isLoading: false,
      error: null,
      data: null,
    },
    verifyEmail: {
      isLoading: false,
      error: null,
      data: null,
    },
  },
  getOnboardingStatus: {
    isLoading: false,
    error: null,
    data: null,
  },
};

export default function registerReducer(
  state = initialState,
  action: RegisterAction,
): RegisterState {
  switch (action.type) {
    case GET_COMPANY_DROPDOWN_SUCCESS:
      return {
        ...state,
        getCompanyDropdown: {
          ...state.getCompanyDropdown,
          isLoading: false,
          data: action.payload,
        },
      };
    case GET_COMPANY_DROPDOWN_FAILED:
      return {
        ...state,
        getCompanyDropdown: {
          ...state.getCompanyDropdown,
          isLoading: false,
          error: action.payload,
        },
      };

    case GET_STATES_SUCCESS:
      return getStates(state, action.payload);
    case GET_STATES_FAILED:
      return {
        ...state,
        getStates: {
          ...state.getStates,
          isLoading: false,
          error: action.payload,
        },
      };
    case GET_STATES_PROGRESS:
      return {
        ...state,
        getStates: {
          ...state.getStates,
          isLoading: action.payload,
        },
      };
    case CREATE_INFORMATION_SUCCESS:
      return {
        ...state,
        createInformation: {
          ...state.createInformation,
          isLoading: false,
          data: action.payload,
        },
      };
    case CREATE_INFORMATION_FAILED:
      return {
        ...state,
        createInformation: {
          ...state.createInformation,
          isLoading: false,
          error: action.payload,
        },
      };
    case SAVE_YOUR_EXPERIENCE_SUCCESS:
      return {
        ...state,
        saveYourExperience: {
          ...state.saveYourExperience,
          isLoading: false,
          data: action.payload,
        },
      };
    case SAVE_YOUR_EXPERIENCE_FAILED:
      return {
        ...state,
        saveYourExperience: {
          ...state.saveYourExperience,
          isLoading: false,
          error: action.payload,
        },
      };
    case SAVE_YOUR_EXPERIENCE_REQUESTED:
      return {
        ...state,
        saveYourExperience: {
          ...state.saveYourExperience,
          formRequested: action.payload,
        },
      };
    case SAVE_WORK_REFERENCE_SUCCESS:
      return {
        ...state,
        saveWorkReference: {
          ...state.saveWorkReference,
          isLoading: false,
          data: action.payload,
        },
      };
    case SAVE_WORK_REFERENCE_FAILED:
      return {
        ...state,
        saveWorkReference: {
          ...state.saveWorkReference,
          isLoading: false,
          error: action.payload,
        },
      };
    case SAVE_WORK_REFERENCE_REQUESTED:
      return {
        ...state,
        saveWorkReference: {
          ...state.saveWorkReference,
          formRequested: action.payload,
        },
      };
    case SHOW_DATE_PICKER_MODAL:
      return {
        ...state,
        datePickerModal: {
          ...state.datePickerModal,
          isVisible: true,
        },
      };
    case HIDE_DATE_PICKER_MODAl:
      return {
        ...state,
        datePickerModal: {
          ...state.datePickerModal,
          isVisible: false,
        },
      };
    case SET_DATE_BIRTH_OF_USER:
      return {
        ...state,
        datePickerModal: {
          ...state.datePickerModal,
          date: action.payload,
        },
      };
    case GET_ONBOARDING_QUESTION_SUCCESS:
      return getOnboardingQuestion(state, action.payload);
    case GET_ONBOARDING_QUESTION_FAILED:
      return {
        ...state,
        getAllOnboardingQuestion: {
          ...state.getAllOnboardingQuestion,
          isLoading: false,
          error: action.payload,
        },
      };
    case GET_ONBOARDING_QUESTION_PROGRESS:
      return {
        ...state,
        getAllOnboardingQuestion: {
          ...state.getAllOnboardingQuestion,
          isLoading: action.payload,
        },
      };
    case TOGGLE_COMPANY_CHECKBOX:
      return toggleCompanyCheckbox(state, action.payload);
    case TOGGLE_QUESTION_ANSWER:
      return toggleQuestionAnswer(state, action.payload);
    case CLEAR_QUESTION_ANSWERS:
      return {
        ...state,
        questionAnswers: [],
      };
    case ADD_QUESTION_ANSWER_IMAGES:
      return addQuestionAnswerImages(state, action.payload);
    case REMOVE_QUESTION_ANSWER_IMAGE:
      return removeQuestionAnswerImage(state, action.payload);
    case SET_ERROR_QUESTION:
      return {
        ...state,
        errorQuestion: action.payload,
      };
    case CLEAR_ERROR_QUESTION:
      return {
        ...state,
        errorQuestion: [],
      };
    case SAVE_YOUR_TEST_SUCCESS:
      return {
        ...state,
        saveYourTest: {
          ...state.saveYourTest,
          isLoading: false,
          data: action.payload,
        },
      };
    case SAVE_YOUR_TEST_FAILED:
      return {
        ...state,
        saveYourTest: {
          ...state.saveYourTest,
          isLoading: false,
          error: action.payload,
        },
      };
    case VERIFY_OTP_ACCOUNT_SUCCESS:
      return {
        ...state,
        verifyOtpAccount: {
          ...state.verifyOtpAccount,
          [action.payload.type]: {
            ...state.verifyOtpAccount[
            action.payload.type as 'verifyPhone' | 'verifyEmail'
            ],
            isLoading: false,
            error: null,
            data: action.payload.data,
          },
        },
      };
    case VERIFY_OTP_ACCOUNT_FAILED:
      return {
        ...state,
        verifyOtpAccount: {
          ...state.verifyOtpAccount,
          [action.payload.type]: {
            ...state.verifyOtpAccount[
            action.payload.type as 'verifyPhone' | 'verifyEmail'
            ],
            isLoading: false,
            error: action.payload.error,
            data: null,
          },
        },
      };
    case SEND_OTP_VERIFY_ACCOUNT_SUCCESS:
      return {
        ...state,
        sendOTPVerifyAccount: {
          ...state.sendOTPVerifyAccount,
          [action.payload.type]: {
            ...state.sendOTPVerifyAccount[
            action.payload.type as 'verifyPhone' | 'verifyEmail'
            ],
            isLoading: false,
            error: null,
            data: action.payload.data,
          },
        },
        verifyOtpAccount: {
          ...state.verifyOtpAccount,
          [action.payload.type]: {
            ...state.verifyOtpAccount[
            action.payload.type as 'verifyPhone' | 'verifyEmail'
            ],
            isLoading: false,
            error: null,
            data: null,
          },
        },
      };
    case SEND_OTP_VERIFY_ACCOUNT_FAILED:
      return {
        ...state,
        sendOTPVerifyAccount: {
          ...state.sendOTPVerifyAccount,
          [action.payload.type]: {
            ...state.sendOTPVerifyAccount[
            action.payload.type as 'verifyPhone' | 'verifyEmail'
            ],
            isLoading: false,
            error: action.payload.error,
            data: null,
          },
        },
      };
    case GET_ONBOARDING_STATUS_SUCCESS:
      return {
        ...state,
        getOnboardingStatus: {
          ...state.getOnboardingStatus,
          isLoading: false,
          data: action.payload,
        },
      };
    case GET_ONBOARDING_STATUS_FAILED:
      return {
        ...state,
        getOnboardingStatus: {
          ...state.getOnboardingStatus,
          isLoading: false,
          error: action.payload,
        },
      };
    case GET_ONBOARDING_STATUS_PROGRESS:
      return {
        ...state,
        getOnboardingStatus: {
          ...state.getOnboardingStatus,
          isLoading: action.payload,
        },
      };
    case CLEAR_ONBOARDING_STATUS:
      return {
        ...state,
        getOnboardingStatus: {
          ...state.getOnboardingStatus,
          isLoading: false,
          error: null,
          data: null,
        },
      };
    case CLEAR_STATE_REGISTER_ASYNC:
      return initialState;
    default:
      return state;
  }
}

const getStates = (state: RegisterState, payload: any) => {
  const finalData = payload?.map((item: StateData) => ({
    value: item.key,
    label: `${item.value} - ${item.description}`,
    description: item.description,
  }));

  return {
    ...state,
    getStates: { ...state.getStates, isLoading: false, data: finalData },
  };
};

const toggleCompanyCheckbox = (state: RegisterState, payload: string) => {
  return {
    ...state,
    getCompanyDropdown: {
      ...state.getCompanyDropdown,
      data: state.getCompanyDropdown?.data?.map((item: StateData) =>
        item.key === payload ? { ...item, isChecked: !item.isChecked } : item,
      ),
    },
  };
};

const getOnboardingQuestion = (state: RegisterState, payload: any) => {
  const stateData = Array.isArray(state.getAllOnboardingQuestion.data)
    ? state.getAllOnboardingQuestion.data
    : [];

  const exists = stateData.some(item => item.id === payload.id);

  const newData = exists
    ? stateData.map(item => (item.id === payload.id ? payload : item))
    : [...stateData, payload];

  return {
    ...state,
    getAllOnboardingQuestion: {
      ...state.getAllOnboardingQuestion,
      isLoading: false,
      data: newData,
    },
  };
};

const toggleQuestionAnswer = (
  state: RegisterState,
  payload: { questionId: string; optionId: string },
) => {
  // Remove all existing answers for this question first
  const filteredAnswers = state.questionAnswers.filter(
    answer => answer.questionId !== payload.questionId,
  );
  const errorQuestion = state.errorQuestion.filter(item => item !== payload.questionId);

  // Add the new selected answer
  const newAnswers = [
    ...filteredAnswers,
    { ...payload, isChecked: true, images: [] },
  ];

  return {
    ...state,
    questionAnswers: newAnswers,
    errorQuestion,
  };
};

const addQuestionAnswerImages = (
  state: RegisterState,
  payload: {
    questionId: string;
    optionId: string;
    images: {
      path: string;
      fileName: string;
      typeFile: string;
    }[];
  },
) => {
  const updatedAnswers = state.questionAnswers.map(answer => {
    if (
      answer.questionId === payload.questionId &&
      answer.optionId === payload.optionId
    ) {
      return {
        ...answer,
        images: [...(answer.images || []), ...payload.images],
      };
    }
    return answer;
  });

  return {
    ...state,
    questionAnswers: updatedAnswers,
  };
};

const removeQuestionAnswerImage = (
  state: RegisterState,
  payload: { questionId: string; optionId: string; imageIndex: number },
) => {
  const updatedAnswers = state.questionAnswers.map(answer => {
    if (
      answer.questionId === payload.questionId &&
      answer.optionId === payload.optionId
    ) {
      const updatedImages = [...(answer.images || [])];
      updatedImages.splice(payload.imageIndex, 1);
      return {
        ...answer,
        images: updatedImages,
      };
    }
    return answer;
  });

  return {
    ...state,
    questionAnswers: updatedAnswers,
  };
};
