import {
  CLEAR_PENDING_CONTRACT_DETAIL,
  CLEAR_SAVE_SIGNATURE_CONTRACT,
  CLEAR_SIGNED_CONTRACT,
  CLEAR_TEMPLATE_CONTRACT_DETAIL,
  GET_PENDING_CONTRACT_ASYNC,
  GET_PENDING_CONTRACT_FAILED,
  GET_PENDING_CONTRACT_PROGRESS,
  GET_PENDING_CONTRACT_SUCCESS,
  GET_SIGNED_CONTRACT_ASYNC,
  GET_SIGNED_CONTRACT_FAILED,
  GET_SIGNED_CONTRACT_PROGRESS,
  GET_SIGNED_CONTRACT_SUCCESS,
  GET_TEMPLATE_CONTRACT_DETAIL_ASYNC,
  GET_TEMPLATE_CONTRACT_DETAIL_FAILED,
  GET_TEMPLATE_CONTRACT_DETAIL_PROGRESS,
  GET_TEMPLATE_CONTRACT_DETAIL_SUCCESS,
  SAVE_SIGNATURE_CONTRACT_ASYNC,
  SAVE_SIGNATURE_CONTRACT_FAILED,
  SAVE_SIGNATURE_CONTRACT_PROGRESS,
  SAVE_SIGNATURE_CONTRACT_SUCCESS,
} from './contants';
import {
  GetTemplateContractDetailPayload,
  SaveSignatureContractPayload,
} from './types';

// saga
export const getTemplateContractDetailAsync = (
  payload: GetTemplateContractDetailPayload,
) => ({
  type: GET_TEMPLATE_CONTRACT_DETAIL_ASYNC,
  payload,
});

export const getPendingContractAsync = () => ({
  type: GET_PENDING_CONTRACT_ASYNC,
  payload: null,
});

export const saveSignatureContractAsync = (
  payload: SaveSignatureContractPayload,
) => ({
  type: SAVE_SIGNATURE_CONTRACT_ASYNC,
  payload,
});

export const getSignedContractAsync = () => ({
  type: GET_SIGNED_CONTRACT_ASYNC,
  payload: null,
});
// reducer
export const getTemplateContractDetailSuccess = (payload: any) => ({
  type: GET_TEMPLATE_CONTRACT_DETAIL_SUCCESS,
  payload,
});

export const getTemplateContractDetailFailed = (payload: any) => ({
  type: GET_TEMPLATE_CONTRACT_DETAIL_FAILED,
  payload,
});

export const getTemplateContractDetailProgress = (payload: boolean) => ({
  type: GET_TEMPLATE_CONTRACT_DETAIL_PROGRESS,
  payload,
});

export const clearTemplateContractDetail = () => ({
  type: CLEAR_TEMPLATE_CONTRACT_DETAIL,
  payload: null,
});

export const getPendingContractSuccess = (payload: any) => ({
  type: GET_PENDING_CONTRACT_SUCCESS,
  payload,
});

export const getPendingContractFailed = (payload: any) => ({
  type: GET_PENDING_CONTRACT_FAILED,
  payload,
});

export const getPendingContractProgress = (payload: boolean) => ({
  type: GET_PENDING_CONTRACT_PROGRESS,
  payload,
});

export const clearPendingContractDetail = () => ({
  type: CLEAR_PENDING_CONTRACT_DETAIL,
  payload: null,
});

export const saveSignatureContractSuccess = (payload: any) => ({
  type: SAVE_SIGNATURE_CONTRACT_SUCCESS,
  payload,
});

export const saveSignatureContractFailed = (payload: any) => ({
  type: SAVE_SIGNATURE_CONTRACT_FAILED,
  payload,
});

export const saveSignatureContractProgress = (payload: boolean) => ({
  type: SAVE_SIGNATURE_CONTRACT_PROGRESS,
  payload,
});

export const clearSaveSignatureContract = () => ({
  type: CLEAR_SAVE_SIGNATURE_CONTRACT,
  payload: null,
});

export const getSignedContractSuccess = (payload: any) => ({
  type: GET_SIGNED_CONTRACT_SUCCESS,
  payload,
});

export const getSignedContractFailed = (payload: any) => ({
  type: GET_SIGNED_CONTRACT_FAILED,
  payload,
});

export const getSignedContractProgress = (payload: boolean) => ({
  type: GET_SIGNED_CONTRACT_PROGRESS,
  payload,
});

export const clearSignedContractDetail = () => ({
  type: CLEAR_SIGNED_CONTRACT,
  payload: null,
});
export const actionContract = {
  getTemplateContractDetailAsync,
  getTemplateContractDetailSuccess,
  getTemplateContractDetailFailed,
  getTemplateContractDetailProgress,
  clearTemplateContractDetail,
  getPendingContractAsync,
  getPendingContractSuccess,
  getPendingContractFailed,
  getPendingContractProgress,
  clearPendingContractDetail,
  saveSignatureContractAsync,
  saveSignatureContractSuccess,
  saveSignatureContractFailed,
  saveSignatureContractProgress,
  clearSaveSignatureContract,
  getSignedContractAsync,
  getSignedContractSuccess,
  getSignedContractFailed,
  getSignedContractProgress,
  clearSignedContractDetail,
};

export type ContractAction =
  | ReturnType<typeof getTemplateContractDetailAsync>
  | ReturnType<typeof getTemplateContractDetailSuccess>
  | ReturnType<typeof getTemplateContractDetailFailed>
  | ReturnType<typeof getTemplateContractDetailProgress>
  | ReturnType<typeof clearTemplateContractDetail>
  | ReturnType<typeof getPendingContractAsync>
  | ReturnType<typeof getPendingContractSuccess>
  | ReturnType<typeof getPendingContractFailed>
  | ReturnType<typeof getPendingContractProgress>
  | ReturnType<typeof clearPendingContractDetail>
  | ReturnType<typeof saveSignatureContractAsync>
  | ReturnType<typeof saveSignatureContractSuccess>
  | ReturnType<typeof saveSignatureContractFailed>
  | ReturnType<typeof saveSignatureContractProgress>
  | ReturnType<typeof clearSaveSignatureContract>
  | ReturnType<typeof getSignedContractAsync>
  | ReturnType<typeof getSignedContractSuccess>
  | ReturnType<typeof getSignedContractFailed>
  | ReturnType<typeof getSignedContractProgress>
  | ReturnType<typeof clearSignedContractDetail>;
