import { ContractAction } from './actions';
import {
  CLEAR_PENDING_CONTRACT_DETAIL,
  CLEAR_SAVE_SIGNATURE_CONTRACT,
  CLEAR_SIGNED_CONTRACT,
  CLEAR_TEMPLATE_CONTRACT_DETAIL,
  GET_PENDING_CONTRACT_FAILED,
  GET_PENDING_CONTRACT_PROGRESS,
  GET_PENDING_CONTRACT_SUCCESS,
  GET_SIGNED_CONTRACT_FAILED,
  GET_SIGNED_CONTRACT_PROGRESS,
  GET_SIGNED_CONTRACT_SUCCESS,
  GET_TEMPLATE_CONTRACT_DETAIL_FAILED,
  GET_TEMPLATE_CONTRACT_DETAIL_PROGRESS,
  GET_TEMPLATE_CONTRACT_DETAIL_SUCCESS,
  SAVE_SIGNATURE_CONTRACT_FAILED,
  SAVE_SIGNATURE_CONTRACT_PROGRESS,
  SAVE_SIGNATURE_CONTRACT_SUCCESS,
} from './contants';
import { ContractState } from './types';

const initialState: ContractState = {
  templateContractDetail: {
    isLoading: false,
    error: null,
    data: null,
  },
  pendingContract: {
    isLoading: false,
    error: null,
    data: [],
  },
  saveSignatureContract: {
    isLoading: false,
    error: null,
    data: null,
  },
  signedContract: {
    isLoading: false,
    error: null,
    data: [],
  },
};

export default function contractReducer(
  state = initialState,
  action: ContractAction,
): ContractState {
  switch (action.type) {
    case GET_TEMPLATE_CONTRACT_DETAIL_SUCCESS:
      return {
        ...state,
        templateContractDetail: {
          ...state.templateContractDetail,
          isLoading: false,
          data: action.payload,
        },
      };
    case GET_TEMPLATE_CONTRACT_DETAIL_FAILED:
      return {
        ...state,
        templateContractDetail: {
          ...state.templateContractDetail,
          isLoading: false,
          error: action.payload,
          data: null,
        },
      };
    case GET_TEMPLATE_CONTRACT_DETAIL_PROGRESS:
      return {
        ...state,
        templateContractDetail: {
          ...state.templateContractDetail,
          isLoading: action.payload,
        },
      };
    case CLEAR_TEMPLATE_CONTRACT_DETAIL:
      return {
        ...initialState,
        templateContractDetail: {
          ...initialState.templateContractDetail,
          data: null,
        },
      };
    case GET_PENDING_CONTRACT_SUCCESS:
      return {
        ...state,
        pendingContract: {
          ...state.pendingContract,
          isLoading: false,
          data: action.payload,
        },
      };
    case GET_PENDING_CONTRACT_FAILED:
      return {
        ...state,
        pendingContract: {
          ...state.pendingContract,
          isLoading: false,
          error: action.payload,
        },
      };
    case GET_PENDING_CONTRACT_PROGRESS:
      return {
        ...state,
        pendingContract: {
          ...state.pendingContract,
          isLoading: action.payload,
        },
      };
    case CLEAR_PENDING_CONTRACT_DETAIL:
      return {
        ...initialState,
        pendingContract: {
          ...initialState.pendingContract,
          data: [],
        },
      };

    case SAVE_SIGNATURE_CONTRACT_SUCCESS:
      return {
        ...state,
        saveSignatureContract: {
          ...state.saveSignatureContract,
          isLoading: false,
          data: action.payload,
        },
      };
    case SAVE_SIGNATURE_CONTRACT_FAILED:
      return {
        ...state,
        saveSignatureContract: {
          ...state.saveSignatureContract,
          isLoading: false,
          error: action.payload,
        },
      };
    case SAVE_SIGNATURE_CONTRACT_PROGRESS:
      return {
        ...state,
        saveSignatureContract: {
          ...state.saveSignatureContract,
          isLoading: action.payload,
        },
      };
    case CLEAR_SAVE_SIGNATURE_CONTRACT:
      return {
        ...initialState,
        saveSignatureContract: {
          ...initialState.saveSignatureContract,
          data: null,
        },
      };
    case GET_SIGNED_CONTRACT_SUCCESS:
      return {
        ...state,
        signedContract: {
          ...state.signedContract,
          isLoading: false,
          data: action.payload,
        },
      };
    case GET_SIGNED_CONTRACT_FAILED:
      return {
        ...state,
        signedContract: {
          ...state.signedContract,
          isLoading: false,
          error: action.payload,
        },
      };
    case GET_SIGNED_CONTRACT_PROGRESS:
      return {
        ...state,
        signedContract: {
          ...state.signedContract,
          isLoading: action.payload,
        },
      };
    case CLEAR_SIGNED_CONTRACT:
      return {
        ...initialState,
        signedContract: {
          ...initialState.signedContract,
          data: [],
        },
      };
    default:
      return state;
  }
}
