import { call, put, takeEvery } from 'redux-saga/effects';
import { logger } from '../../../utils';
import { HIDE_LOADING_OVERLAY, SHOW_LOADING_OVERLAY } from '../app/constants';
import {
  fetchGetKeyBiometric,
  fetchLogin,
  fetchLoginBiometric,
  fetchProfile,
  fetchRegisterLoginBiometric,
} from '../../../api/login';
import { ResponseApi } from '../../store/types';
import {
  CLEAR_BIOMETRIC_AUTHENTICATION_ASYNC,
  CLEAR_BIOMETRIC_AUTHENTICATION_SUCCESS,
  LOGIN_ASYNC,
  LOGIN_BIOMETRIC_ASYNC,
  LOGIN_BIOMETRIC_FAILED,
  LOGIN_BIOMETRIC_SUCCESS,
  LOGIN_FAILED,
  LOGIN_SUCCESS,
  REGISTER_BIOMETRIC_AUTHENTICATION,
  REGISTER_BIOMETRIC_AUTHENTICATION_FAILED,
  REGISTER_BIOMETRIC_AUTHENTICATION_SUCCESS,
  SET_BIOMETRIC_AUTHENTICATION,
  SET_USER_PROFILE,
  UNREGISTER_BIOMETRIC_AUTHENTICATION_ASYNC,
} from './constans';
import { store } from '../../store';
import { STORAGE_KEY, StorageService } from '../../../services/StorageService';
import ReactNativeBiometrics from 'react-native-biometrics';
import Toast from 'react-native-toast-message';
import i18n from '../../../i18n';

function* handleGetProfile(): Generator<any, void, any> {
  try {
    const getProfile = (yield call(fetchProfile)) as ResponseApi;

    if (getProfile.status !== 200) {
      yield put({
        type: LOGIN_FAILED,
        payload: getProfile.message || getProfile.data?.message,
      });
      return;
    }

    yield put({
      type: SET_USER_PROFILE,
      payload: getProfile.data,
    });
  } catch (error) {
    logger('error handleGetProfile', error);
  } finally {
    yield put({ type: HIDE_LOADING_OVERLAY });
  }
}

function* handleLogin(action: {
  type: string;
  payload: any;
}): Generator<any, void, any> {
  try {
    yield put({ type: SHOW_LOADING_OVERLAY });
    yield put({ type: LOGIN_FAILED, payload: null });

    const response = (yield call(fetchLogin, action.payload)) as ResponseApi;

    if (response.status !== 200) {
      yield put({
        type: LOGIN_FAILED,
        payload: response.message || response.data?.message,
      });
      return;
    }
    yield put({
      type: LOGIN_SUCCESS,
      payload: {
        token: response.data.token,
        refreshToken: response.data.refreshToken,
      },
    });

    yield call(handleGetProfile);
  } catch (error) {
    logger('error handleLogin', error);
    yield put({
      type: LOGIN_FAILED,
      payload: error instanceof Error ? error.message : 'Login failed',
    });
  } finally {
    yield put({ type: HIDE_LOADING_OVERLAY });
  }
}

function* handleSetBiometricAuthentication(action: {
  type: string;
  payload: string;
}): Generator<any, void, any> {
  try {
    yield put({ type: SHOW_LOADING_OVERLAY });
    const userProfile = store.getState().auth.userProfile;

    const response = (yield call(fetchRegisterLoginBiometric, {
      userName: userProfile?.userName,
      biometricKey: action.payload,
    })) as ResponseApi;
    if (response.status !== 200) {
      yield put({
        type: REGISTER_BIOMETRIC_AUTHENTICATION_FAILED,
        payload: response.message || response.data?.message,
      });
      return;
    }
    yield put({ type: REGISTER_BIOMETRIC_AUTHENTICATION_SUCCESS });

    const infoBiometric = {
      biometricKey: action.payload,
      email: userProfile?.email,
      userName: userProfile?.userName,
    };
    StorageService.set(STORAGE_KEY.BIOMETRIC_KEY, infoBiometric);
    Toast.show({
      text1: i18n.t('BIOMETRIC_REGISTERED_SUCCESSFULLY'),
      type: 'success',
    });
  } catch (error) {
    logger('error handleSetBiometricAuthentication', error);
  } finally {
    yield put({ type: HIDE_LOADING_OVERLAY });
  }
}

function* handleUnregisterBiometricAuthenticationAsync(): Generator<
  any,
  void,
  any
> {
  try {
    yield put({ type: SET_BIOMETRIC_AUTHENTICATION, payload: false });
    StorageService.delete(STORAGE_KEY.BIOMETRIC_KEY);
  } catch (error) {
    logger('error handleUnregisterBiometricAuthenticationAsync', error);
  }
}

function* handleLoginBiometric(): Generator<any, void, any> {
  try {
    const rnBiometrics = new ReactNativeBiometrics();
    yield put({ type: SHOW_LOADING_OVERLAY });
    const infoBiometric = (yield call(
      StorageService.get,
      STORAGE_KEY.BIOMETRIC_KEY,
    )) as { userName: string };

    const getKeyBiometric = (yield call(
      fetchGetKeyBiometric,
      infoBiometric?.userName,
    )) as ResponseApi;

    if (getKeyBiometric.status !== 200) {
      yield put({
        type: LOGIN_BIOMETRIC_FAILED,
        payload: getKeyBiometric.message || getKeyBiometric.data?.message,
      });
      return;
    }

    const { success, signature } = (yield call(
      [rnBiometrics, rnBiometrics.createSignature],
      {
        promptMessage: 'Biometric authentication',
        payload: getKeyBiometric?.data,
      },
    )) as { success: boolean; signature: string };

    if (!success) {
      yield put({
        type: LOGIN_BIOMETRIC_FAILED,
        payload: 'BIOMETRIC_AUTHENTICATION_FAILED',
      });
      return;
    }

    const response = (yield call(fetchLoginBiometric, {
      username: infoBiometric?.userName,
      signature: signature,
    })) as ResponseApi;
    if (response.status !== 200) {
      yield put({
        type: LOGIN_BIOMETRIC_FAILED,
        payload: response.message || response.data?.message,
      });
    }
    yield put({
      type: LOGIN_BIOMETRIC_SUCCESS,
      payload: {
        token: response.data.token,
        refreshToken: response.data.refreshToken,
      },
    });
    yield call(handleGetProfile);
  } catch (error) {
    logger('error handleLoginBiometric', error);
  } finally {
    yield put({ type: HIDE_LOADING_OVERLAY });
  }
}

export function* handleClearBiometricAuthenticationAsync(): Generator<
  any,
  void,
  any
> {
  try {
    StorageService.delete(STORAGE_KEY.BIOMETRIC_KEY);
    yield put({ type: CLEAR_BIOMETRIC_AUTHENTICATION_SUCCESS });
  } catch (error) {
    logger('error handleClearBiometricAuthenticationAsync', error);
  } finally {
    yield put({ type: HIDE_LOADING_OVERLAY });
  }
}

export default function* appSaga(): Generator<any, void, any> {
  yield takeEvery(LOGIN_ASYNC, handleLogin);
  yield takeEvery(
    REGISTER_BIOMETRIC_AUTHENTICATION,
    handleSetBiometricAuthentication,
  );
  yield takeEvery(
    UNREGISTER_BIOMETRIC_AUTHENTICATION_ASYNC,
    handleUnregisterBiometricAuthenticationAsync,
  );
  yield takeEvery(LOGIN_BIOMETRIC_ASYNC, handleLoginBiometric);
  yield takeEvery(
    CLEAR_BIOMETRIC_AUTHENTICATION_ASYNC,
    handleClearBiometricAuthenticationAsync,
  );
}
