import {
  CLEAR_MESSAGES_BY_ROOM_ID,
  CLEAR_MY_ROOMS,
  CLEAR_UNREAD_MESSAGE,
  GET_MESSAGES_BY_ROOM_ID_ASYNC,
  GET_MESSAGES_BY_ROOM_ID_FAILED,
  GET_MESSAGES_BY_ROOM_ID_PROGRESS,
  GET_MESSAGES_BY_ROOM_ID_SUCCESS,
  GET_MY_ROOMS_ASYNC,
  GET_MY_ROOMS_FAILED,
  GET_MY_ROOMS_PROGRESS,
  GET_MY_ROOMS_SUCCESS,
  GET_UNREAD_MESSAGE_ASYNC,
  GET_UNREAD_MESSAGE_FAILED,
  GET_UNREAD_MESSAGE_PROGRESS,
  GET_UNREAD_MESSAGE_SUCCESS,
} from './constants';

export const getMyRoomsAsync = () => ({ type: GET_MY_ROOMS_ASYNC } as const);
export const getMyRoomsSuccess = (payload: any) =>
  ({ type: GET_MY_ROOMS_SUCCESS, payload } as const);
export const getMyRoomsFailed = (payload: any) =>
  ({ type: GET_MY_ROOMS_FAILED, payload } as const);
export const getMyRoomsProgress = (payload: boolean) =>
  ({ type: GET_MY_ROOMS_PROGRESS, payload } as const);
export const clearMyRooms = () => ({ type: CLEAR_MY_ROOMS } as const);

export const getMessagesByRoomIdAsync = (payload: any) =>
  ({ type: GET_MESSAGES_BY_ROOM_ID_ASYNC, payload } as const);
export const getMessagesByRoomIdSuccess = (payload: any) =>
  ({ type: GET_MESSAGES_BY_ROOM_ID_SUCCESS, payload } as const);
export const getMessagesByRoomIdFailed = (payload: any) =>
  ({ type: GET_MESSAGES_BY_ROOM_ID_FAILED, payload } as const);
export const getMessagesByRoomIdProgress = (payload: boolean) =>
  ({ type: GET_MESSAGES_BY_ROOM_ID_PROGRESS, payload } as const);
export const clearMessagesByRoomId = () =>
  ({ type: CLEAR_MESSAGES_BY_ROOM_ID } as const);

export const getUnreadMessageAsync = () =>
  ({ type: GET_UNREAD_MESSAGE_ASYNC } as const);
export const getUnreadMessageSuccess = (payload: any) =>
  ({ type: GET_UNREAD_MESSAGE_SUCCESS, payload } as const);
export const getUnreadMessageFailed = (payload: any) =>
  ({ type: GET_UNREAD_MESSAGE_FAILED, payload } as const);
export const getUnreadMessageProgress = (payload: boolean) =>
  ({ type: GET_UNREAD_MESSAGE_PROGRESS, payload } as const);
export const clearUnreadMessage = () =>
  ({ type: CLEAR_UNREAD_MESSAGE } as const);

export const actionMessage = {
  getMyRoomsAsync,
  getMyRoomsSuccess,
  getMyRoomsFailed,
  getMyRoomsProgress,
  clearMyRooms,
  getMessagesByRoomIdAsync,
  getMessagesByRoomIdSuccess,
  getMessagesByRoomIdFailed,
  getMessagesByRoomIdProgress,
  clearMessagesByRoomId,
  getUnreadMessageAsync,
  getUnreadMessageSuccess,
  getUnreadMessageFailed,
  getUnreadMessageProgress,
  clearUnreadMessage,
};

export type MessageAction = ReturnType<
  | typeof getMyRoomsAsync
  | typeof getMyRoomsSuccess
  | typeof getMyRoomsFailed
  | typeof getMyRoomsProgress
  | typeof clearMyRooms
  | typeof getMessagesByRoomIdAsync
  | typeof getMessagesByRoomIdSuccess
  | typeof getMessagesByRoomIdFailed
  | typeof getMessagesByRoomIdProgress
  | typeof clearMessagesByRoomId
  | typeof getUnreadMessageAsync
  | typeof getUnreadMessageSuccess
  | typeof getUnreadMessageFailed
  | typeof getUnreadMessageProgress
  | typeof clearUnreadMessage
>;
