import { all, fork } from 'redux-saga/effects';
import appSaga from '../features/app/saga';
import authSaga from '../features/auth/saga';
import forgotPasswordSaga from '../features/forgotPassword/saga';
import verifyOtpSaga from '../features/verifyOtp/saga';
import changePasswordSaga from '../features/changePassword/saga';
import registerSaga from '../features/register/saga';
import homeSaga from '../features/home/saga';
import referFriendSaga from '../features/referFriend/saga';
import networkSaga from '../features/network/saga';
import userSaga from '../features/user/saga';
import contractSaga from '../features/contract/saga';
import roleTypeSaga from '../features/roleType/saga';
import registerCompanySaga from '../features/registerCompany/saga';
import paymentInfoW9Saga from '../features/paymentInfoW9/saga';
import trainingSaga from '../features/training/saga';
import jobBoardSaga from '../features/jobBoard/saga';
import myJobSaga from '../features/myjob/saga';
import jobDetailSaga from '../features/jobDetail/saga';
import messageSaga from '../features/message/saga';

export default function* rootSaga() {
  yield all([
    fork(appSaga),
    fork(authSaga),
    fork(forgotPasswordSaga),
    fork(verifyOtpSaga),
    fork(changePasswordSaga),
    fork(registerSaga),
    fork(homeSaga),
    fork(referFriendSaga),
    fork(networkSaga),
    fork(userSaga),
    fork(contractSaga),
    fork(roleTypeSaga),
    fork(registerCompanySaga),
    fork(paymentInfoW9Saga),
    fork(trainingSaga),
    fork(jobBoardSaga),
    fork(myJobSaga),
    fork(jobDetailSaga),
    fork(messageSaga),
  ]);
}
