export type BaseField = {
  label: string;
  placeholder: string;
  isRequired?: boolean;
  type?: 'text' | 'select' | 'custom' | 'textarea';
};

export type SelectField = BaseField & {
  type: 'select';
  options: BottomSelectorOption[];
};

export type CustomField = BaseField & {
  type: 'custom';
  render: (props?: any) => React.JSX.Element;
};

export type GroupField = {
  field: FieldItem[];
};

export type FieldItem = BaseField | SelectField | CustomField | GroupField;

export type SectionItem = {
  title?: string;
  data: FieldItem[];
};

