import { Image, StyleSheet, View } from 'react-native';
import { Button, SCREEN_HEIGHT, Text } from 'tradewize-component';
import colors from '../../../theme/colors';
import { useAppTranslation } from '../../../hooks/useAppTranslation';
import { SvgXml } from 'react-native-svg';
import { IconImage } from '../../../assets/svg';
import { FormikErrors, FormikTouched } from 'formik';
import { globalStyles } from '../../../global/styles';
import { handleTakePhoto } from '../../../helper/register';
import { useState } from 'react';

interface IdentificationPhotoProps {
  errors: FormikErrors<Record<string, string>>;
  touched: FormikTouched<Record<string, string>>;
  setTouched: (touched: Record<string, boolean>) => void;
  setFieldValue: (
    field: string,
    value: { uri: string; type: string; fileName: string },
  ) => void;
}

const IdentificationPhoto = (props: IdentificationPhotoProps) => {
  const { errors, touched, setFieldValue, setTouched } = props;
  const { t } = useAppTranslation();

  const [img, setImg] = useState<{
    uri: string;
    error: string;
  }>({
    uri: '',
    error: '',
  });

  return (
    <View style={styles.root}>
      <View
        style={[
          styles.container,
          errors?.PHOTO_IDENTIFICATION &&
            touched?.PHOTO_IDENTIFICATION && { borderColor: colors.red },
        ]}
      >
        <Button
          style={styles.button}
          variant="primary"
          title={t('TAKE_PHOTO')}
          textStyle={styles.txtBtn}
          onPress={() => {
            handleTakePhoto(setFieldValue, setImg);
            setTouched({
              ...touched,
              ['PHOTO_IDENTIFICATION']: true,
            } as Record<string, boolean>);
          }}
        />

        <View style={styles.img}>
          {img?.uri ? (
            <Image
              source={{ uri: img?.uri }}
              style={styles.imgTakePhoto}
              resizeMode="contain"
            />
          ) : (
            <SvgXml
              xml={IconImage(colors.mainColorPrimary)}
              width={32}
              height={32}
            />
          )}
        </View>
      </View>
      {errors?.PHOTO_IDENTIFICATION && touched?.PHOTO_IDENTIFICATION && (
        <Text numberOfLines={2} style={globalStyles.errorText}>
          {errors?.PHOTO_IDENTIFICATION}
        </Text>
      )}
    </View>
  );
};

const styles = StyleSheet.create({
  root: {
    marginBottom: 16,
  },
  container: {
    paddingHorizontal: 16,
    paddingVertical: 12,
    borderWidth: 1,
    borderStyle: 'dashed',
    borderRadius: 8,
    marginBottom: 4,
    borderColor: colors.mainColorTertiary,
    marginTop: 4,
  },
  button: {
    backgroundColor: colors.mainColorPrimary,
    marginBottom: 12,
  },
  txtBtn: {
    fontWeight: 'bold',
  },
  img: {
    alignItems: 'center',
    justifyContent: 'center',
    height: SCREEN_HEIGHT * 0.1,
  },
  imgTakePhoto: {
    width: '100%',
    height: '100%',
  },
});

export default IdentificationPhoto;
