import { call, put, takeEvery } from 'redux-saga/effects';
import { fetchResetPassword } from '../../../api/changelePassword';
import {
  CHANGE_PASSWORD_ASYNC,
  CHANGE_PASSWORD_FAILURE,
  CHANGE_PASSWORD_SUCCESS,
} from './contants';
import { HIDE_LOADING_OVERLAY, SHOW_LOADING_OVERLAY } from '../app/constants';
import { ChangePasswordPayload } from './types';
import { ResponseApi } from '../../store/types';
import { logger } from '../../../utils';
import Toast from 'react-native-toast-message';
import i18n from '../../../i18n';
import { NavigationService } from '../../../services/NavigationService';
import { SCREENS_ENUM } from '../../../types/global';

export const handleChangePassword = function* (action: {
  type: string;
  payload: ChangePasswordPayload;
}) {
  try {
    yield put({ type: SHOW_LOADING_OVERLAY });
    const response = (yield call(
      fetchResetPassword,
      action.payload,
    )) as ResponseApi;
    if (response.status === 200) {
      yield put({ type: CHANGE_PASSWORD_SUCCESS });
      yield call(Toast.show, {
        text1: i18n.t('CHANGE_PASSWORD_SUCCESS'),
        type: 'success',
      });
      yield call(NavigationService.reset, SCREENS_ENUM.LoginScreen);
    } else {
      yield put({
        type: CHANGE_PASSWORD_FAILURE,
        payload: response?.message || i18n.t('CHANGE_PASSWORD_FAILED'),
      });
    }
  } catch (error) {
    logger('error handleChangePassword', error);
    yield put({
      type: CHANGE_PASSWORD_FAILURE,
      payload: i18n.t('CHANGE_PASSWORD_FAILED'),
    });
  } finally {
    yield put({ type: HIDE_LOADING_OVERLAY });
  }
};

export default function* changePasswordSaga(): Generator<any, void, any> {
  yield takeEvery(CHANGE_PASSWORD_ASYNC, handleChangePassword);
}
