import {
  GET_WORK_ORDER_DETAIL_ASYNC,
  GET_WORK_ORDER_DETAIL_SUCCESS,
  GET_WORK_ORDER_DETAIL_FAILED,
  GET_WORK_ORDER_DETAIL_PROGRESS,
  CLEAR_WORK_ORDER_DETAIL,
  GET_MATERIAL_LIST_ASYNC,
  GET_MATERIAL_LIST_SUCCESS,
  GET_MATERIAL_LIST_FAILED,
  GET_MATERIAL_LIST_PROGRESS,
  GET_CHAT_GROUP_ASYNC,
  GET_CHAT_GROUP_PROGRESS,
  GET_CHAT_GROUP_FAILED,
  GET_CHAT_GROUP_SUCCESS,
  GET_CHECKLIST_SUCCESS,
  CLEAR_CHECKLIST,
  GET_CHECKLIST_PROGRESS,
  GET_CHECKLIST_FAILED,
  GET_COMMENT_CHECKLIST_ASYNC,
  GET_COMMENT_CHECKLIST_SUCCESS,
  GET_COMMENT_CHECKLIST_FAILED,
  GET_COMMENT_CHECKLIST_PROGRESS,
  CLEAR_COMMENT_CHECKLIST,
} from './contants';
import {
  GetChatGroupPayload,
  GetMaterialListPayload,
  JobDetailType,
  MaterialItem,
  ChatGroup,
  ChecklistItem,
  CommentPayload,
} from './types';

// action saga
export const getWorkOrderDetailAsync = (payload: {
  id: string;
  getJobPersonalRoomChat?: boolean;
}) =>
  ({
    type: GET_WORK_ORDER_DETAIL_ASYNC,
    payload,
  } as const);

export const getMaterialListAsync = (payload: GetMaterialListPayload) =>
  ({
    type: GET_MATERIAL_LIST_ASYNC,
    payload,
  } as const);

export const getChatGroupAsync = (payload: GetChatGroupPayload) =>
  ({
    type: GET_CHAT_GROUP_ASYNC,
    payload,
  } as const);

export const getCommentChecklistAsync = (payload: CommentPayload) =>
  ({
    type: GET_COMMENT_CHECKLIST_ASYNC,
    payload,
  } as const);

// action reducer
export const getWorkOrderDetailSuccess = (payload: JobDetailType) =>
  ({
    type: GET_WORK_ORDER_DETAIL_SUCCESS,
    payload,
  } as const);

export const getWorkOrderDetailFailed = (payload: any) =>
  ({
    type: GET_WORK_ORDER_DETAIL_FAILED,
    payload,
  } as const);

export const getWorkOrderDetailProgress = (payload: boolean) =>
  ({
    type: GET_WORK_ORDER_DETAIL_PROGRESS,
    payload,
  } as const);

export const clearWorkOrderDetail = () =>
  ({
    type: CLEAR_WORK_ORDER_DETAIL,
  } as const);

export const getMaterialListSuccess = (payload: MaterialItem[]) =>
  ({
    type: GET_MATERIAL_LIST_SUCCESS,
    payload,
  } as const);

export const getMaterialListFailed = (payload: any) =>
  ({
    type: GET_MATERIAL_LIST_FAILED,
    payload,
  } as const);

export const getMaterialListProgress = (payload: boolean) =>
  ({
    type: GET_MATERIAL_LIST_PROGRESS,
    payload,
  } as const);

export const getChatGroupSuccess = (payload: ChatGroup) =>
  ({
    type: GET_CHAT_GROUP_SUCCESS,
    payload,
  } as const);

export const getChatGroupFailed = (payload: any) =>
  ({
    type: GET_CHAT_GROUP_FAILED,
    payload,
  } as const);

export const getChatGroupProgress = (payload: boolean) =>
  ({
    type: GET_CHAT_GROUP_PROGRESS,
    payload,
  } as const);

export const getChecklistSuccess = (payload: ChecklistItem[]) =>
  ({
    type: GET_CHECKLIST_SUCCESS,
    payload,
  } as const);

export const getChecklistFailed = (payload: any) =>
  ({
    type: GET_CHECKLIST_FAILED,
    payload,
  } as const);

export const getChecklistProgress = (payload: boolean) =>
  ({
    type: GET_CHECKLIST_PROGRESS,
    payload,
  } as const);

export const clearChecklist = () =>
  ({
    type: CLEAR_CHECKLIST,
  } as const);

export const clearCommentChecklist = () =>
  ({
    type: CLEAR_COMMENT_CHECKLIST,
  } as const);

export const getCommentChecklistSuccess = (payload: ChecklistItem) =>
  ({
    type: GET_COMMENT_CHECKLIST_SUCCESS,
    payload,
  } as const);

export const getCommentChecklistFailed = (payload: any) =>
  ({
    type: GET_COMMENT_CHECKLIST_FAILED,
    payload,
  } as const);

export const getCommentChecklistProgress = (payload: boolean) =>
  ({
    type: GET_COMMENT_CHECKLIST_PROGRESS,
    payload,
  } as const);

export const actionJobDetail = {
  getWorkOrderDetailAsync,
  getWorkOrderDetailSuccess,
  getWorkOrderDetailFailed,
  getWorkOrderDetailProgress,
  clearWorkOrderDetail,
  // material list
  getMaterialListAsync,
  getMaterialListSuccess,
  getMaterialListFailed,
  getMaterialListProgress,
  // chat group
  getChatGroupAsync,
  getChatGroupSuccess,
  getChatGroupFailed,
  getChatGroupProgress,
  // checklist
  getChecklistSuccess,
  getChecklistFailed,
  getChecklistProgress,
  clearChecklist,
  // comment checklist
  getCommentChecklistAsync,
  getCommentChecklistSuccess,
  getCommentChecklistFailed,
  getCommentChecklistProgress,
  clearCommentChecklist,
};

export type JobDetailAction = ReturnType<
  | typeof getWorkOrderDetailAsync
  | typeof getWorkOrderDetailSuccess
  | typeof getWorkOrderDetailFailed
  | typeof getWorkOrderDetailProgress
  | typeof clearWorkOrderDetail
  | typeof getMaterialListAsync
  | typeof getMaterialListSuccess
  | typeof getMaterialListFailed
  | typeof getMaterialListProgress
  | typeof getChatGroupAsync
  | typeof getChatGroupSuccess
  | typeof getChatGroupFailed
  | typeof getChatGroupProgress
  | typeof getChecklistSuccess
  | typeof getChecklistFailed
  | typeof getChecklistProgress
  | typeof clearChecklist
  | typeof getCommentChecklistAsync
  | typeof getCommentChecklistSuccess
  | typeof getCommentChecklistFailed
  | typeof getCommentChecklistProgress
  | typeof clearCommentChecklist
>;
