import {
  GET_ROLE_TYPE_ASYNC,
  GET_ROLE_TYPE_FAILED,
  GET_ROLE_TYPE_PROGRESS,
  GET_ROLE_TYPE_SUCCESS,
} from './contants';

// action saga
export const getRoleTypeAsync = () => ({ type: GET_ROLE_TYPE_ASYNC } as const);

// action reducer
export const getRoleTypeSuccess = (payload: any) =>
  ({ type: GET_ROLE_TYPE_SUCCESS, payload } as const);
export const getRoleTypeFailed = (payload: any) =>
  ({ type: GET_ROLE_TYPE_FAILED, payload } as const);
export const getRoleTypeProgress = (payload: boolean) =>
  ({ type: GET_ROLE_TYPE_PROGRESS, payload } as const);

export const actionRoleType = {
  getRoleTypeAsync,
  getRoleTypeSuccess,
  getRoleTypeFailed,
  getRoleTypeProgress,
};

export type RoleTypeAction = ReturnType<
  | typeof getRoleTypeAsync
  | typeof getRoleTypeSuccess
  | typeof getRoleTypeFailed
  | typeof getRoleTypeProgress
>;
