import { logger } from '../utils';
import { client } from './configAxios';
import { getImportantJobs, getMyTodayJobs } from './endpoint';

export const fetchMyJobsToday = async () => {
  try {
    const response = await client.get(getMyTodayJobs);
    return {
      status: response.status,
      code: response.status,
      data: response.data?.data,
    };
  } catch (error: any) {
    logger('error fetchMyJobsToday', error);
    return {
      message: error?.response?.data?.message,
      code: error?.code,
      status: error?.status,
    };
  }
};

export const fetchImportantJobs = async () => {
  try {
    const response = await client.get(getImportantJobs, {
      params: { IsCutOff: true },
    });
    return {
      status: response.status,
      code: response.status,
      data: response.data?.data,
    };
  } catch (error: any) {
    logger('error fetchImportantJobs', error);
    return {
      message: error?.response?.data?.message,
      code: error?.code,
      status: error?.status,
    };
  }
};
