import { ReferFriendAction } from './actions';
import {
  GET_COMPANY_PROGRESS,
  GET_COMPANY_SUCCESS,
  GET_COMPANY_TEST_PROGRESS,
  GET_COMPANY_TEST_SUCCESS,
  REGISTER_COMPANY_PROGRESS,
  REGISTER_COMPANY_SUCCESS,
} from './contants';
import { CompanyAndRoleData } from './types';

export const initialState: CompanyAndRoleData = {
  data: [],
  loading: false,
};

export default function registerCompanyReducer(
  state = initialState,
  action: ReferFriendAction,
): CompanyAndRoleData {
  switch (action.type) {
    case GET_COMPANY_SUCCESS:
      return {
        ...state,
        data: action.payload,
      };
    case GET_COMPANY_PROGRESS:
      return {
        ...state,
        loading: action.payload,
      };
    case REGISTER_COMPANY_SUCCESS:
      return {
        ...state,
        data: action.payload,
      };
    case REGISTER_COMPANY_PROGRESS:
      return {
        ...state,
        loading: action.payload,
      };
    case GET_COMPANY_TEST_SUCCESS:
      return {
        ...state,
        data: action.payload,
      };
    case GET_COMPANY_TEST_PROGRESS:
      return {
        ...state,
        loading: action.payload,
      };

    default:
      return state;
  }
}
