import {
  LOGIN_ASYNC,
  LOGIN_FAILED,
  LOGIN_SUCCESS,
  LOGOUT,
  SET_BIOMETRIC_AUTHENTICATION,
  REGISTER_BIOMETRIC_AUTHENTICATION,
  SET_NOTIFICATION_TOKEN,
  SET_USER_PROFILE,
  UPDATE_USER_PROFILE,
  REGISTER_BIOMETRIC_AUTHENTICATION_SUCCESS,
  REGISTER_BIOMETRIC_AUTHENTICATION_FAILED,
  UNREGISTER_BIOMETRIC_AUTHENTICATION_ASYNC,
  LOGIN_BIOMETRIC_ASYNC,
  LOGIN_BIOMETRIC_SUCCESS,
  LOGIN_BIOMETRIC_FAILED,
  IS_LOGIN_BIOMETRIC_AUTHENTICATION,
  CLEAR_BIOMETRIC_AUTHENTICATION_ASYNC,
  CLEAR_BIOMETRIC_AUTHENTICATION_SUCCESS,
} from './constans';
import { LoginPayload } from './types';

// action saga
export const loginAsync = (payload: LoginPayload) =>
  ({ type: LOGIN_ASYNC, payload } as const);

export const loginBiometricAsync = () =>
  ({ type: LOGIN_BIOMETRIC_ASYNC } as const);
export const unregisterBiometricAuthenticationAsync = () =>
  ({ type: UNREGISTER_BIOMETRIC_AUTHENTICATION_ASYNC } as const);
// action reducer
export const loginFailed = (payload: string) =>
  ({ type: LOGIN_FAILED, payload } as const);
export const loginSuccess = (payload: {
  token: string;
  refreshToken: string;
}) => ({ type: LOGIN_SUCCESS, payload } as const);
export const setUserProfile = (payload: any) =>
  ({ type: SET_USER_PROFILE, payload } as const);
export const logout = () => ({ type: LOGOUT } as const);
export const setNotificationToken = (payload: string) =>
  ({ type: SET_NOTIFICATION_TOKEN, payload } as const);
export const updateUserProfile = (payload: { [key: string]: any }) =>
  ({ type: UPDATE_USER_PROFILE, payload } as const);
export const setBiometricAuthentication = (payload: boolean) =>
  ({ type: SET_BIOMETRIC_AUTHENTICATION, payload } as const);
export const registerBiometricAuthentication = (biometricKey: string) =>
  ({ type: REGISTER_BIOMETRIC_AUTHENTICATION, payload: biometricKey } as const);
export const registerBiometricAuthenticationFailed = (payload: string) =>
  ({ type: REGISTER_BIOMETRIC_AUTHENTICATION_FAILED, payload } as const);
export const registerBiometricAuthenticationSuccess = () =>
  ({ type: REGISTER_BIOMETRIC_AUTHENTICATION_SUCCESS } as const);
export const loginBiometricFailed = (payload: string) =>
  ({ type: LOGIN_BIOMETRIC_FAILED, payload } as const);
export const loginBiometricSuccess = (payload: {
  token: string;
  refreshToken: string;
}) => ({ type: LOGIN_BIOMETRIC_SUCCESS, payload } as const);
export const isLoginBiometricAuthentication = (payload: boolean) =>
  ({ type: IS_LOGIN_BIOMETRIC_AUTHENTICATION, payload } as const);
export const clearBiometricAuthenticationAsync = () =>
  ({ type: CLEAR_BIOMETRIC_AUTHENTICATION_ASYNC } as const);
export const clearBiometricAuthenticationSuccess = () =>
  ({ type: CLEAR_BIOMETRIC_AUTHENTICATION_SUCCESS } as const);

export const actionAuth = {
  loginAsync,
  loginFailed,
  loginSuccess,
  setUserProfile,
  logout,
  setNotificationToken,
  updateUserProfile,
  setBiometricAuthentication,
  registerBiometricAuthentication,
  registerBiometricAuthenticationFailed,
  registerBiometricAuthenticationSuccess,
  unregisterBiometricAuthenticationAsync,
  loginBiometricAsync,
  loginBiometricFailed,
  loginBiometricSuccess,
  isLoginBiometricAuthentication,
  clearBiometricAuthenticationAsync,
  clearBiometricAuthenticationSuccess,
};

export type AuthAction =
  | ReturnType<typeof loginAsync>
  | ReturnType<typeof loginFailed>
  | ReturnType<typeof loginSuccess>
  | ReturnType<typeof setUserProfile>
  | ReturnType<typeof logout>
  | ReturnType<typeof setNotificationToken>
  | ReturnType<typeof updateUserProfile>
  | ReturnType<typeof setBiometricAuthentication>
  | ReturnType<typeof registerBiometricAuthentication>
  | ReturnType<typeof registerBiometricAuthenticationFailed>
  | ReturnType<typeof registerBiometricAuthenticationSuccess>
  | ReturnType<typeof unregisterBiometricAuthenticationAsync>
  | ReturnType<typeof loginBiometricAsync>
  | ReturnType<typeof loginBiometricFailed>
  | ReturnType<typeof loginBiometricSuccess>
  | ReturnType<typeof isLoginBiometricAuthentication>
  | ReturnType<typeof clearBiometricAuthenticationAsync>
  | ReturnType<typeof clearBiometricAuthenticationSuccess>;
