import { UserAction } from './actions';
import {
  SET_USER_AVATAR_ASYNC,
  SET_USER_AVATAR_FAILED,
  SET_USER_AVATAR_PROGRESS,
  SET_USER_AVATAR_SUCCESS,
  SET_USER_PROFILE_FAILED,
  SET_USER_PROFILE_PROGRESS,
  SET_USER_PROFILE_SUCCESS,
} from './contains';
import { UserState } from './types';

const initialState: UserState = {
  userAvatar: {
    isLoading: false,
    error: null,
    data: null,
  },
  updateUserProfile: {
    isLoading: false,
    error: null,
    data: null,
  },
};

export default function userReducer(
  state = initialState,
  action: UserAction,
): UserState {
  switch (action.type) {
    case SET_USER_AVATAR_ASYNC:
      return { ...state, userAvatar: { ...state.userAvatar, isLoading: true } };
    case SET_USER_AVATAR_PROGRESS:
      return {
        ...state,
        userAvatar: { ...state.userAvatar, isLoading: action.payload },
      };
    case SET_USER_AVATAR_SUCCESS:
      return {
        ...state,
        userAvatar: {
          ...state.userAvatar,
          isLoading: false,
          data: action.payload,
        },
      };
    case SET_USER_AVATAR_FAILED:
      return {
        ...state,
        userAvatar: {
          ...state.userAvatar,
          isLoading: false,
          error: action.payload,
        },
      };
    case SET_USER_PROFILE_SUCCESS:
      return {
        ...state,
        updateUserProfile: {
          ...state.updateUserProfile,
          isLoading: false,
          data: action.payload,
        },
      };
    case SET_USER_PROFILE_FAILED:
      return {
        ...state,
        updateUserProfile: {
          ...state.updateUserProfile,
          isLoading: false,
          error: action.payload,
        },
      };
    case SET_USER_PROFILE_PROGRESS:
      return {
        ...state,
        updateUserProfile: {
          ...state.updateUserProfile,
          isLoading: action.payload,
        },
      };
    default:
      return state;
  }
}
