import {
  GET_IMPORTANT_JOBS_ASYNC,
  GET_IMPORTANT_JOBS_FAILED,
  GET_IMPORTANT_JOBS_PROGRESS,
  GET_IMPORTANT_JOBS_SUCCESS,
  GET_MY_JOBS_TODAY_ASYNC,
  GET_MY_JOBS_TODAY_FAILED,
  GET_MY_JOBS_TODAY_PROGRESS,
  GET_MY_JOBS_TODAY_SUCCESS,
  SET_IS_SHOW_DUE_VIDEO,
} from './contants';

// action saga
export const getMyJobsTodayAsync = () =>
  ({ type: GET_MY_JOBS_TODAY_ASYNC } as const);
export const getImportantJobsAsync = () =>
  ({ type: GET_IMPORTANT_JOBS_ASYNC } as const);

// action reducer
export const getMyJobsTodaySuccess = (payload: any) =>
  ({ type: GET_MY_JOBS_TODAY_SUCCESS, payload } as const);
export const getMyJobsTodayFailed = (payload: any) =>
  ({ type: GET_MY_JOBS_TODAY_FAILED, payload } as const);
export const getMyJobsTodayProgress = (payload: boolean) =>
  ({ type: GET_MY_JOBS_TODAY_PROGRESS, payload } as const);

export const getImportantJobsSuccess = (payload: any) =>
  ({ type: GET_IMPORTANT_JOBS_SUCCESS, payload } as const);
export const getImportantJobsFailed = (payload: any) =>
  ({ type: GET_IMPORTANT_JOBS_FAILED, payload } as const);
export const getImportantJobsProgress = (payload: boolean) =>
  ({ type: GET_IMPORTANT_JOBS_PROGRESS, payload } as const);

export const setIsShowDueVideo = (payload: boolean) =>
  ({ type: SET_IS_SHOW_DUE_VIDEO, payload } as const);

export const actionHome = {
  getMyJobsTodayAsync,
  getMyJobsTodaySuccess,
  getMyJobsTodayFailed,
  getMyJobsTodayProgress,

  getImportantJobsAsync,
  getImportantJobsSuccess,
  getImportantJobsFailed,
  getImportantJobsProgress,

  setIsShowDueVideo,
};

export type HomeAction = ReturnType<
  | typeof getMyJobsTodayAsync
  | typeof getMyJobsTodaySuccess
  | typeof getMyJobsTodayFailed
  | typeof getMyJobsTodayProgress
  | typeof getImportantJobsAsync
  | typeof getImportantJobsSuccess
  | typeof getImportantJobsFailed
  | typeof getImportantJobsProgress
  | typeof setIsShowDueVideo
>;
