import {
  GET_MY_JOBS_SUCCESS,
  GET_MY_JOBS_FAILED,
  GET_MY_JOBS_PROGRESS,
  GET_COMMUNITIES_SUCCESS,
  GET_COMMUNITIES_FAILED,
  GET_COMMUNITIES_PROGRESS,
} from './contants';
import { MyJobsAction } from './actions';
import { MyJobsState } from './types';

export const initialState: MyJobsState = {
  myJobs: {
    isLoading: false,
    error: null,
    data: [],
  },
  communities: {
    isLoading: false,
    error: null,
    data: [],
  },
};

export default function myJobReducer(
  state = initialState,
  action: MyJobsAction,
): MyJobsState {
  switch (action.type) {
    case GET_MY_JOBS_SUCCESS:
      return {
        ...state,
        myJobs: {
          isLoading: false,
          error: null,
          data: action.payload,
        },
      };
    case GET_MY_JOBS_FAILED:
      return {
        ...state,
        myJobs: {
          isLoading: false,
          error: action.payload,
          data: [],
        },
      };
    case GET_MY_JOBS_PROGRESS:
      return {
        ...state,
        myJobs: {
          ...state.myJobs,
          isLoading: action.payload,
        },
      };
    case GET_COMMUNITIES_SUCCESS:
      return {
        ...state,
        communities: {
          isLoading: false,
          error: null,
          data: action.payload,
        },
      };
    case GET_COMMUNITIES_FAILED:
      return {
        ...state,
        communities: {
          isLoading: false,
          error: action.payload,
          data: [],
        },
      };
    case GET_COMMUNITIES_PROGRESS:
      return {
        ...state,
        communities: {
          ...state.communities,
          isLoading: action.payload,
        },
      };
    default:
      return state;
  }
}
