import { GET_NETWORK_SUCCESS, GET_REVIEWS_SUCCESS } from './contants';
import { NetworkAction } from './actions';
import { NetWork } from './types';

export const initialState: NetWork = {
  network: {
    items: [],
    pageIndex: 0,
    pageSize: 0,
    totalCount: 0,
    totalPage: 0,
  },
  reviews: [],
};

export default function homeReducer(
  state = initialState,
  action: NetworkAction,
): NetWork {
  switch (action.type) {
    case GET_NETWORK_SUCCESS:
      return {
        ...state,
        network: action.payload.items,
      };

    case GET_REVIEWS_SUCCESS:
      return {
        ...state,
        reviews: action.payload,
      };

    default:
      return state;
  }
}
