import { StyleSheet } from 'react-native';
import colors from '../../../theme/colors';
import { Button, Text } from 'tradewize-component';
import { IconCalendar } from '../../../assets/svg';
import { SvgXml } from 'react-native-svg';
import dayjs from 'dayjs';
import { useEffect } from 'react';
import { FormikErrors, FormikTouched } from 'formik';
import { FORMAT_DATE_OF_BIRTH } from '../../../utils';
import { globalStyles } from '../../../global/styles';
import { useDispatch, useSelector } from 'react-redux';
import { showDatePickerModal } from '../../../redux/features/register/actions';
import { RootState } from '../../../redux/store/rootReducer';
import { useAppTranslation } from '../../../hooks/useAppTranslation';

interface DateOfBirthProps {
  errors: FormikErrors<Record<string, string>>;
  touched: FormikTouched<Record<string, string>>;
  setTouched: (touched: Record<string, boolean>) => void;
  onChange: (date: string) => void;
}

const DateOfBirth = (props: DateOfBirthProps) => {
  const { t } = useAppTranslation();
  const dispatch = useDispatch();
  const { errors, touched, setTouched, onChange } = props;
  const datePickerModal = useSelector(
    (state: RootState) => state.register.datePickerModal,
  );

  useEffect(() => {
    onChange?.(dayjs(datePickerModal?.date).format('YYYY-MM-DD') || '');
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [datePickerModal?.date]);

  return (
    <>
      <Button
        onPress={() => {
          setTouched({
            ...touched,
            ['DATE_OF_BIRTH']: true,
          } as Record<string, boolean>);
          dispatch(showDatePickerModal());
        }}
        variant="secondary"
        style={[
          styles.container,
          !!(errors?.DATE_OF_BIRTH && touched?.DATE_OF_BIRTH) && {
            borderColor: colors.red,
          },
        ]}
      >
        <Text
          style={[
            styles.text,
            datePickerModal?.date && { color: colors.black },
          ]}
        >
          {datePickerModal?.date
            ? dayjs(datePickerModal?.date).format(FORMAT_DATE_OF_BIRTH)
            : t('DATE_OF_BIRTH')}
        </Text>
        <SvgXml xml={IconCalendar(colors.grayScale70)} width={24} height={24} />
      </Button>
      {errors?.DATE_OF_BIRTH && touched?.DATE_OF_BIRTH && (
        <Text style={globalStyles.errorText}>{errors?.DATE_OF_BIRTH}</Text>
      )}
    </>
  );
};

const styles = StyleSheet.create({
  container: {
    marginTop: 4,
    backgroundColor: colors.additionalSecondary,
    flexDirection: 'row',
    alignItems: 'center',
    justifyContent: 'space-between',
  },
  text: {
    fontSize: 14,
    fontWeight: '400',
    color: colors.grayScale70,
  },
});

export default DateOfBirth;
