import { client } from './configAxios';
import { logger } from '../utils';
import { forgotPassword } from './endpoint';

export const fetchForgotPassword = async (email: string) => {
  try {
    const response = await client.post(forgotPassword, { email });

    return {
      status: response.status,
      code: response.status,
      data: response.data?.data,
    };
  } catch (error: any) {
    logger('error fetchForgotPassword', error);
    return {
      message: error?.response?.data?.message,
      code: error?.code,
      status: error?.status,
    };
  }
};
