import { all, call, delay, put, takeEvery } from 'redux-saga/effects';
import {
  fetchGetPendingContract,
  fetchGetSignedContract,
  fetchGetTemplateContractDetail,
  fetchSaveSignatureContract,
} from '../../../api/contract';
import {
  GET_PENDING_CONTRACT_ASYNC,
  GET_PENDING_CONTRACT_FAILED,
  GET_PENDING_CONTRACT_PROGRESS,
  GET_PENDING_CONTRACT_SUCCESS,
  GET_SIGNED_CONTRACT_ASYNC,
  GET_SIGNED_CONTRACT_FAILED,
  GET_SIGNED_CONTRACT_PROGRESS,
  GET_SIGNED_CONTRACT_SUCCESS,
  GET_TEMPLATE_CONTRACT_DETAIL_ASYNC,
  GET_TEMPLATE_CONTRACT_DETAIL_FAILED,
  GET_TEMPLATE_CONTRACT_DETAIL_PROGRESS,
  GET_TEMPLATE_CONTRACT_DETAIL_SUCCESS,
  SAVE_SIGNATURE_CONTRACT_ASYNC,
  SAVE_SIGNATURE_CONTRACT_FAILED,
  SAVE_SIGNATURE_CONTRACT_PROGRESS,
  SAVE_SIGNATURE_CONTRACT_SUCCESS,
} from './contants';
import {
  GetTemplateContractDetailPayload,
  SaveSignatureContractPayload,
} from './types';
import { updateUserProfile } from '../auth/actions';
import {
  clearPendingContractDetail,
  clearTemplateContractDetail,
} from './actions';
import { getFileFromS3Key } from '../../../utils';

const mapItem = async (item: any) => {
  const pdfUrl = await getFileFromS3Key(item.pdfFile.PdfKey);

  return {
    ...item,
    pdfFile: {
      ...item.pdfFile,
      urlShow: pdfUrl,
    },
  };
};

function* getTemplateContractDetailSaga(action: {
  type: string;
  payload: GetTemplateContractDetailPayload;
}): Generator<any, void, any> {
  try {
    yield put({ type: GET_TEMPLATE_CONTRACT_DETAIL_PROGRESS, payload: true });
    const response = yield call(fetchGetTemplateContractDetail, action.payload);
    if (response.status === 200) {
      yield put({
        type: GET_TEMPLATE_CONTRACT_DETAIL_SUCCESS,
        payload: response.data,
      });
    } else {
      yield put({
        type: GET_TEMPLATE_CONTRACT_DETAIL_FAILED,
        payload: response.message,
      });
    }
  } catch (error) {
    yield put({ type: GET_TEMPLATE_CONTRACT_DETAIL_FAILED, payload: error });
  } finally {
    yield put({ type: GET_TEMPLATE_CONTRACT_DETAIL_PROGRESS, payload: false });
  }
}

function* getPendingContractSaga(): Generator<any, void, any> {
  try {
    yield put({ type: GET_PENDING_CONTRACT_PROGRESS, payload: true });

    const response = yield call(fetchGetPendingContract);
    if (response.status === 200 && response.data !== '204') {
      if (response.data.length > 0) {
        const detailResponse = yield call(fetchGetTemplateContractDetail, {
          companyId: response.data[0]?.companyId,
          userType: response.data[0]?.userType,
        });

        if (detailResponse.status === 200) {
          yield put({
            type: GET_TEMPLATE_CONTRACT_DETAIL_SUCCESS,
            payload: detailResponse.data,
          });
          yield put({
            type: GET_PENDING_CONTRACT_SUCCESS,
            payload: response.data,
          });
        } else {
          yield put({
            type: GET_TEMPLATE_CONTRACT_DETAIL_FAILED,
            payload: detailResponse.message,
          });
        }
      }
    } else {
      yield put(updateUserProfile({ hasContractsToSign: false }));
      yield put(clearTemplateContractDetail());
      yield put(clearPendingContractDetail());
      yield put({
        type: GET_PENDING_CONTRACT_FAILED,
        payload: response.message,
      });
    }
  } catch (error) {
    yield put({ type: GET_PENDING_CONTRACT_FAILED, payload: error });
  } finally {
    yield put({ type: GET_PENDING_CONTRACT_PROGRESS, payload: false });
  }
}

export function* saveSignatureContractSaga(action: {
  type: string;
  payload: SaveSignatureContractPayload;
}): Generator<any, void, any> {
  try {
    yield put({ type: SAVE_SIGNATURE_CONTRACT_PROGRESS, payload: true });
    const response = yield call(fetchSaveSignatureContract, action.payload);
    if (response.status === 200) {
      yield put({
        type: SAVE_SIGNATURE_CONTRACT_SUCCESS,
        payload: response.data,
      });
      yield delay(150);
      yield call(getPendingContractSaga);
    } else {
      yield put({
        type: SAVE_SIGNATURE_CONTRACT_FAILED,
        payload: response.message,
      });
    }
  } catch (error) {
    yield put({ type: SAVE_SIGNATURE_CONTRACT_FAILED, payload: error });
  } finally {
    yield put({ type: SAVE_SIGNATURE_CONTRACT_PROGRESS, payload: false });
  }
}

export function* getSignedContractSaga(): Generator<any, void, any> {
  try {
    yield put({ type: GET_SIGNED_CONTRACT_PROGRESS, payload: true });

    const response = yield call(fetchGetSignedContract);

    if (response.status === 200) {
      const newData = yield all(
        response.data.map((item: any) => call(mapItem, item)),
      );

      yield put({
        type: GET_SIGNED_CONTRACT_SUCCESS,
        payload: newData,
      });
    } else {
      yield put({
        type: GET_SIGNED_CONTRACT_FAILED,
        payload: response.message,
      });
    }
  } catch (error) {
    yield put({
      type: GET_SIGNED_CONTRACT_FAILED,
      payload: error,
    });
  } finally {
    yield put({ type: GET_SIGNED_CONTRACT_PROGRESS, payload: false });
  }
}

export default function* contractSaga() {
  yield takeEvery(
    GET_TEMPLATE_CONTRACT_DETAIL_ASYNC,
    getTemplateContractDetailSaga,
  );
  yield takeEvery(GET_PENDING_CONTRACT_ASYNC, getPendingContractSaga);
  yield takeEvery(SAVE_SIGNATURE_CONTRACT_ASYNC, saveSignatureContractSaga);
  yield takeEvery(GET_SIGNED_CONTRACT_ASYNC, getSignedContractSaga);
}
