import { client } from './configAxios';
import { logger } from '../utils';
import {
  getCompanyDropdown as getCompanyDropdownEndpoint,
  getStates as getStatesEndpoint,
  createInformation as createInformationEndpoint,
  saveYourExperience as saveYourExperienceEndpoint,
  updateInformation as updateInformationEndpoint,
  saveWorkReference as saveWorkReferenceEndpoint,
  getOnboardingQuestion as getOnboardingQuestionEndpoint,
  saveYourTest as saveYourTestEndpoint,
  verifyEmailOrPhone as verifyEmailOrPhoneEndpoint,
  sendOTPVerifyAccount as sendOTPVerifyAccountEndpoint,
  getOnboardingStatus as getOnboardingStatusEndpoint,
} from './endpoint';

export const getCompanyDropdown = async () => {
  try {
    const response = await client.get(getCompanyDropdownEndpoint);
    return {
      status: response.status,
      code: response.status,
      data: response.data?.data,
    };
  } catch (error: any) {
    logger('error fetchGetCompanyDropdown', error);
    return {
      message: error?.response?.data?.message,
      code: error?.code,
      status: error?.status,
    };
  }
};

