import {
  FETCH_HOUSE_HAS_JOB_WORKER_ASYNC,
  FETCH_HOUSE_HAS_JOB_WORKER_FAILED,
  FETCH_HOUSE_HAS_JOB_WORKER_PROGRESS,
  FETCH_HOUSE_HAS_JOB_WORKER_SUCCESS,
  GET_COMMUNITY_USER_HAS_JOB_ASYNC,
  GET_COMMUNITY_USER_HAS_JOB_FAILED,
  GET_COMMUNITY_USER_HAS_JOB_PROGRESS,
  GET_COMMUNITY_USER_HAS_JOB_SUCCESS,
  REGISTER_JOBS_ASYNC,
  REGISTER_JOBS_FAILED,
  REGISTER_JOBS_PROGRESS,
  REGISTER_JOBS_SUCCESS,
  TOGGLE_JOB_BOARD_FAV_ASYNC,
  TOGGLE_JOB_BOARD_FAV_FAILED,
  TOGGLE_JOB_BOARD_FAV_PROGRESS,
  TOGGLE_JOB_BOARD_FAV_SUCCESS,
} from './contants';
import {
  addFavoriteJob,
  fetchHouseHasJobWorker,
  FetchHouseHasJobWorkerParams,
  getCommunityUserHasJob,
  registerJobs,
  unFavoriteJob,
} from '../../../api/jobBoard';
import { call, put, takeEvery } from 'redux-saga/effects';
import { ResponseApi } from '../../store/types';
import { HIDE_LOADING_OVERLAY, SHOW_LOADING_OVERLAY } from '../app/constants';
import { getCurrentLocation, logger } from '../../../utils';
import { store } from '../../store';
import dayjs from 'dayjs';

interface CommunityUserHasJob {
  id: string;
  communityCode: string;
  name: string;
}

const sortData = (data: any[]) => {
  try {
    if (!data) return [];
    const sortedData = data
      ?.sort((a: any, b: any) => {
        if (a.bonusAmount && !b.bonusAmount) return -1;
        if (!a.bonusAmount && b.bonusAmount) return 1;
        return 0;
      })
      ?.map((item: any) => {
        const startDate = dayjs(item.startDate).startOf('day');
        const today = dayjs().startOf('day');
        const diffDays = startDate.diff(today, 'day');
        return {
          ...item,
          isDiffDays: diffDays > 14,
        };
      });
    return sortedData;
  } catch (error) {
    logger('error sortData', error);
    return [];
  }
};

const fetchHouseHasJobWorkerSaga = function* (action: {
  type: string;
  payload: FetchHouseHasJobWorkerParams;
}): Generator<any, void, any> {
  try {
    yield put({ type: FETCH_HOUSE_HAS_JOB_WORKER_PROGRESS, payload: true });
    yield put({ type: SHOW_LOADING_OVERLAY });
    const response = (yield call(
      fetchHouseHasJobWorker,
      action.payload,
    )) as ResponseApi;
    if (response.status !== 200) {
      yield put({
        type: FETCH_HOUSE_HAS_JOB_WORKER_FAILED,
        payload: response.message,
      });
      return;
    }
    const sortedData = yield call(sortData, response?.data || []);

    yield put({
      type: FETCH_HOUSE_HAS_JOB_WORKER_SUCCESS,
      payload: sortedData,
    });
  } catch (error) {
    logger('error fetchHouseHasJobWorkerSaga', error);
  } finally {
    yield put({ type: FETCH_HOUSE_HAS_JOB_WORKER_PROGRESS, payload: false });
    yield put({ type: HIDE_LOADING_OVERLAY });
  }
};

const toggleJobBoardFavSaga = function* (action: {
  type: string;
  payload: string;
}): Generator<any, void, any> {
  try {
    const stateJobBoard = store.getState().jobBoard.houseHasJobWorker.data;
    yield put({ type: TOGGLE_JOB_BOARD_FAV_PROGRESS, payload: true });
    const itemSelected = stateJobBoard?.find(
      (item: any) => item.jobId === action.payload,
    );
    if (!itemSelected) {
      yield put({
        type: TOGGLE_JOB_BOARD_FAV_FAILED,
        payload: 'Item not found',
      });
      return;
    }
    const isAddFavorite = itemSelected?.isFavorite;
    let response: ResponseApi;
    if (isAddFavorite) {
      response = (yield call(unFavoriteJob, action.payload)) as ResponseApi;
    } else {
      response = (yield call(addFavoriteJob, action.payload)) as ResponseApi;
    }

    if (response.status !== 200) {
      yield put({
        type: TOGGLE_JOB_BOARD_FAV_FAILED,
        payload: response.message,
      });
      return;
    }
    const newData = stateJobBoard?.map((item: any) =>
      item?.jobId === action.payload
        ? { ...item, isFavorite: !isAddFavorite }
        : item,
    );

    yield put({ type: TOGGLE_JOB_BOARD_FAV_SUCCESS, payload: newData });
  } catch (error) {
    logger('error toggleJobBoardFavSaga', error);
  } finally {
    yield put({ type: TOGGLE_JOB_BOARD_FAV_PROGRESS, payload: false });
  }
};

const registerJobsSaga = function* (action: {
  type: string;
  payload: { jobIds: string[]; message: string };
}): Generator<any, void, any> {
  try {
    yield put({ type: REGISTER_JOBS_PROGRESS, payload: true });
    const response = (yield call(registerJobs, action.payload)) as ResponseApi;
    if (response.status !== 200) {
      yield put({
        type: REGISTER_JOBS_FAILED,
        payload: response.message,
      });
      return;
    }

    const currentData = yield call(getCurrentLocation);

    let payload = {
      jobIds: action.payload?.jobIds,
    } as FetchHouseHasJobWorkerParams;

    if (currentData) {
      payload = {
        ...payload,
        Latitude: currentData?.latitude,
        Longitude: currentData?.longitude,
      };
    }

    const resRefreshData = (yield call(
      fetchHouseHasJobWorker,
      payload,
    )) as ResponseApi;
    if (resRefreshData.status !== 200) {
      return;
    }
    const sortedData = yield call(sortData, resRefreshData?.data || []);
    yield put({ type: REGISTER_JOBS_SUCCESS, payload: sortedData });
  } catch (error) {
    logger('error registerJobsSaga', error);
  } finally {
    yield put({ type: REGISTER_JOBS_PROGRESS, payload: false });
  }
};

const getCommunityUserHasJobSaga = function* (): Generator<any, void, any> {
  try {
    yield put({ type: GET_COMMUNITY_USER_HAS_JOB_PROGRESS, payload: true });
    const response = (yield call(getCommunityUserHasJob)) as ResponseApi;
    if (response.status !== 200) {
      yield put({
        type: GET_COMMUNITY_USER_HAS_JOB_FAILED,
        payload: response.message,
      });
      return;
    }
    const convertData = response.data?.map((item: CommunityUserHasJob) => {
      return {
        label: item.name,
        value: item.id,
        communityCode: item.communityCode,
      };
    });
    yield put({
      type: GET_COMMUNITY_USER_HAS_JOB_SUCCESS,
      payload: convertData,
    });
  } catch (error) {
    logger('error getCommunityUserHasJobSaga', error);
  } finally {
    yield put({ type: GET_COMMUNITY_USER_HAS_JOB_PROGRESS, payload: false });
  }
};

export default function* jobBoardSaga() {
  yield takeEvery(FETCH_HOUSE_HAS_JOB_WORKER_ASYNC, fetchHouseHasJobWorkerSaga);
  yield takeEvery(TOGGLE_JOB_BOARD_FAV_ASYNC, toggleJobBoardFavSaga);
  yield takeEvery(REGISTER_JOBS_ASYNC, registerJobsSaga);
  yield takeEvery(GET_COMMUNITY_USER_HAS_JOB_ASYNC, getCommunityUserHasJobSaga);
}
