import { call, put, takeEvery } from 'redux-saga/effects';
import {
  GET_IMPORTANT_JOBS_ASYNC,
  GET_IMPORTANT_JOBS_FAILED,
  GET_IMPORTANT_JOBS_PROGRESS,
  GET_IMPORTANT_JOBS_SUCCESS,
  GET_MY_JOBS_TODAY_ASYNC,
  GET_MY_JOBS_TODAY_FAILED,
  GET_MY_JOBS_TODAY_PROGRESS,
  GET_MY_JOBS_TODAY_SUCCESS,
} from './contants';
import { fetchImportantJobs, fetchMyJobsToday } from '../../../api/home';
import { ResponseApi } from '../../store/types';

function countTotalItems(
  data: Record<string, any[]>,
  excludeKeys: string[] = [],
): number {
  let total = 0;

  for (const key in data) {
    if (excludeKeys.includes(key)) continue; // b qua key ny
    if (Array.isArray(data[key])) {
      total += data[key].length;
    }
  }

  return total;
}

const getMyJobsToday = function* (): Generator<any, void, any> {
  try {
    yield put({ type: GET_MY_JOBS_TODAY_PROGRESS, payload: true });
    const response = (yield call(fetchMyJobsToday)) as ResponseApi;
    if (response.status !== 200) {
      yield put({ type: GET_MY_JOBS_TODAY_FAILED, payload: response.message });
      return;
    }
    yield put({ type: GET_MY_JOBS_TODAY_SUCCESS, payload: response.data });
  } catch (error) {
    yield put({ type: GET_MY_JOBS_TODAY_FAILED, payload: error });
  } finally {
    yield put({ type: GET_MY_JOBS_TODAY_PROGRESS, payload: false });
  }
};

const getImportantJobs = function* (): Generator<any, void, any> {
  try {
    yield put({ type: GET_IMPORTANT_JOBS_PROGRESS, payload: true });
    const response = (yield call(fetchImportantJobs)) as ResponseApi;
    if (response.status !== 200) {
      yield put({ type: GET_IMPORTANT_JOBS_FAILED, payload: response.message });
      return;
    }
    const totalJobs = countTotalItems(response.data, [
      'verifyMaterialLeft',
      'verifyMaterialVPO',
      'warrantyJobs',
    ]);

    yield put({
      type: GET_IMPORTANT_JOBS_SUCCESS,
      payload: { arrayJobs: response.data, totalJobs },
    });
  } catch (error) {
    yield put({ type: GET_IMPORTANT_JOBS_FAILED, payload: error });
  } finally {
    yield put({ type: GET_IMPORTANT_JOBS_PROGRESS, payload: false });
  }
};

export default function* homeSaga() {
  yield takeEvery(GET_MY_JOBS_TODAY_ASYNC, getMyJobsToday);
  yield takeEvery(GET_IMPORTANT_JOBS_ASYNC, getImportantJobs);
}
